import 'bundles/lazy-loaders.service.js';
import ReservationUnit from 'shared/services/inventory/reservation-unit.service';
import ReservationField from 'shared/services/setup/reservation-field.service';

export default angular.module('deskworks.reservations', [
  'deskworks.state',
  'deskworks.routeHelper',
  'deskworks.setup.service',
  'deskworks.userProfile.service',
  'deskworks.center.service',
  'deskworks.calendarService',
  'deskworks.ReservationPrice',
  'deskworks.lazyLoaders',
  ReservationUnit.name,
  ReservationField.name,
])

  .config(function($routeProvider) {

    /**
     * User Reservations
     */
    $routeProvider.when('/reservations', {
      template: '<div ng-include="\'user-reservations/user-reservations.tpl.html\'"></div>',
      controller: 'UserReservationsCtrl as $ctrl',
      label: 'Reservations',
      routeBranch: 'reservations',
      translations: ['user-reservations'],
      resolve: {
        reservationUnits: (dwAlerts, routeHelper, state, ReservationUnit) => {
          return routeHelper.auth().then(() => {
            return ReservationUnit.query(state.getCurrentCenterId())
              .catch(err => dwAlerts.error(err, 'Failed to query reservation units.'));
          });
        },
        user: (dwAlerts, userProfileService, state, routeHelper) => {
          return routeHelper.auth().then(() => {
            return routeHelper.selectUser().then(() => {
              return userProfileService.get(state.getCurrentCenterId(), state.getCurrentUserId())
                .catch(err => dwAlerts.error(err, 'Failed to get user profile.'));
            });
          });
        },
        settings: function(dwAlerts, setupService) {
          return setupService.getSetupSettings()
            .catch(err => dwAlerts.error(err, 'Failed to get setup settings.'));
        },
        reservationFields: (dwAlerts, ReservationField) => ReservationField.query()
          .catch(err => dwAlerts.error(err, 'Failed to query reservation fields.')),
        lazy: 'lazyLoadReservations',
      }
    });

    /**
     * Reservation Calendar
     */
    $routeProvider.when('/reservation-calendar', {
      template: '<rc-calendar cal-data="$resolve.calData" settings="$resolve.settings" ff-bulk-read-only></rc-calendar>',
      label: 'Reservation Calendar',
      routeBranch: 'reservation-calendar',
      reloadOnSearch: false,
      hideBanner: true,
      translations: ['reservations'],
      resolve: {
        calData: function($q, $filter, state, helper, routeHelper, dwAlerts, calendarService, rcHelper, resolveTimezone) {
          return $q.all([routeHelper.auth(), resolveTimezone()]).then(() => {
            var date = $filter('toDate')(state.getParam('date'), 'Z');
            date = moment.isMoment(date) ? date : moment();
            return calendarService.query(
              state.getCurrentCenterId(),
              rcHelper.weekStart(date), rcHelper.weekEnd(date),
              state.getParam('role')
            )
              .catch(err => dwAlerts.error(err, 'Failed to fetch calendar data.'));
          });
        },
        settings: function(dwAlerts, setupService) {
          return setupService.getSetupSettings()
            .catch(err => dwAlerts.error(err, 'Failed to get setup settings.'));
        },
        lazy: 'lazyLoadReservations',
      }
    });

    $routeProvider.when('/new-reservation', {
      template: '<res-reservation center="$resolve.center" user="$resolve.user" cal-data="$resolve.calData"' +
        ' hide-reservation-cost-estimate="$resolve.settings.hideReservationCostEstimate"' +
        ' hide-recurring-reservations="$resolve.settings.hideRecurringReservations"' +
        ' hide-staff-only-units="$resolve.settings.hideStaffOnlyUnits"' +
        ' reservation-prices="$resolve.reservationPrices" reservation-fields="$resolve.reservationFields"' +
        ' ff-bulk-read-only></res-reservation>',
      label: 'New Reservation',
      routeBranch: 'new-reservation',
      hideBanner: true,
      reloadOnSearch: false,
      translations: ['reservations'],
      resolve: {
        user: function($q, userProfileService, state, dwAlerts, routeHelper, resolveTimezone) {
          return $q.all([routeHelper.auth(), resolveTimezone()]).then(() => {
            return userProfileService.get(state.getCurrentCenterId(), state.getCurrentUserId())
              .catch(err => dwAlerts.error(err, 'Failed to get user profile.'));
          });
        },
        center: function(state, dwAlerts, centerService, routeHelper) {
          return routeHelper.auth().then(() => {
            return centerService.get(state.getCurrentCenterId())
              .catch(err => dwAlerts.error(err, 'Failed to get center.'));
          });
        },
        calData: function($q, $filter, state, helper, dwAlerts, calendarService, routeHelper, resolveTimezone) {
          return $q.all([routeHelper.auth(), resolveTimezone()]).then(() => {
            let date = $filter('toDate')(state.getParam('date'), 'Z');
            date = moment.isMoment(date) ? date : moment();
            return calendarService.query(state.getCurrentCenterId(), date, date, state.getParam('role'))
              .catch(err => dwAlerts.error(err, 'Failed to fetch calendar data.'));
          });
        },
        reservationPrices: function(state, dwAlerts, ReservationPrice, routeHelper) {
          return routeHelper.auth().then(() => {
            return ReservationPrice.query(state.getCurrentCenterId(), state.getCurrentUserId())
              .catch(err => dwAlerts.error(err, 'Failed to query reservation prices.'));
          });
        },
        settings: function(dwAlerts, setupService) {
          return setupService.getSetupSettings()
            .catch(err => dwAlerts.error(err, 'Failed to get setup settings.'));
        },
        reservationFields: (dwAlerts, ReservationField) => ReservationField.query()
          .catch(err => dwAlerts.error(err, 'Failed to query reservation fields.')),
        lazy: 'lazyLoadReservations',
      }
    });

    $routeProvider.when('/edit-reservation/:reservationId', {
      template: '<res-reservation center="$resolve.center" user="$resolve.user" cal-data="$resolve.calData"' +
        ' hide-reservation-cost-estimate="$resolve.settings.hideReservationCostEstimate"' +
        ' hide-staff-only-units="$resolve.settings.hideStaffOnlyUnits"' +
        ' reservation-prices="$resolve.reservationPrices" reservation-fields="$resolve.reservationFields"' +
        ' ff-bulk-read-only></res-reservation>',
      label: 'Edit Reservation',
      routeBranch: 'reservation-calendar',
      reloadOnSearch: false,
      hideBanner: true,
      translations: ['reservations'],
      resolve: {
        user: function($q, userProfileService, state, dwAlerts, routeHelper, resolveTimezone) {
          return $q.all([routeHelper.auth(), resolveTimezone()]).then(() => {
            return userProfileService.get(state.getCurrentCenterId(), state.getCurrentUserId())
              .catch(err => dwAlerts.error(err, 'Failed to get user profile.'));
          });
        },
        center: function(state, dwAlerts, centerService, routeHelper) {
          return routeHelper.auth().then(() => {
            return centerService.get(state.getCurrentCenterId())
              .catch(err => dwAlerts.error(err, 'Failed to get center.'));
          });
        },
        calData: function($q, $filter, state, helper, dwAlerts, calendarService, routeHelper, resolveTimezone) {
          return $q.all([routeHelper.auth(), resolveTimezone()]).then(() => {
            let date = $filter('toDate')(state.getParam('date'), 'Z');
            date = moment.isMoment(date) ? date : moment();
            return calendarService.query(state.getCurrentCenterId(), date, date, state.getParam('role'))
              .catch(err => dwAlerts.error(err, 'Failed to fetch calendar data.'));
          });
        },
        reservationPrices: function(state, dwAlerts, ReservationPrice, routeHelper) {
          return routeHelper.auth().then(() => {
            return ReservationPrice.query(state.getCurrentCenterId(), state.getCurrentUserId())
              .catch(err => dwAlerts.error(err, 'Failed to query reservation prices.'));
          });
        },
        settings: function(dwAlerts, setupService) {
          return setupService.getSetupSettings()
            .catch(err => dwAlerts.error(err, 'Failed to get setup settings.'));
        },
        reservationFields: (dwAlerts, ReservationField) => ReservationField.query()
          .catch(err => dwAlerts.error(err, 'Failed to query reservation fields.')),
        lazy: 'lazyLoadReservations',
      }
    });
  });
