export default angular.module('deskworks.bundlesConfig', [
  'ngResource',
])

  .provider('bundlesConfig', function() {

    const bundles = [
      { id: 'dashboard', enabled: true, required: true, name: 'Dashboard' },
      { id: 'reservations', enabled: true, required: true, name: 'Reservations' },
      { id: 'usage', enabled: true, required: true, name: 'Usage' },
      { id: 'notifications', enabled: true, required: true, name: 'Notifications' },
      { id: 'communications', enabled: true, required: true, name: 'Communications' },
      { id: 'accounting', enabled: true, required: true, name: 'Accounting' },
      { id: 'inventory', enabled: true, required: true, name: 'Inventory' },
      { id: 'system', enabled: true, required: true, name: 'System' },
      { id: 'reports', enabled: true, required: true, },
      { id: 'setup', enabled: true, required: true, name: 'Setup' },
      { id: 'reports', enabled: true, required: true, name: 'Reports' },
      { id: 'white-label', enabled: true, required: true, name: 'White-Label' },
      { id: 'import', enabled: true, required: false, name: 'Import' },
      { id: 'printer-usage', enabled: true, required: false, name: 'Printer Usage' },
      { id: 'floor-plans', enabled: true, required: false, name: 'Floor Plans' },
      { id: 'check-in-form', enabled: true, required: false, name: 'Check In Form' },
      { id: 'termination-request', enabled: false, required: false, name: 'Termination Request' },
      { id: 'payments-assignment', enabled: true, required: false, name: 'Payments Assignment' },
      { id: 'deskworks-sweep', enabled: false, required: false, name: 'Deskworks Sweep' },

      // Wizards
      { id: 'simple-reservation', enabled: true, required: false, name: 'Simple Reservation wizard' },
      { id: 'bundle-reservation', enabled: false, required: false, name: 'Bundle Reservation wizard' },
      { id: 'buy-pass', enabled: true, required: false, name: 'Buy A Pass wizard' },
      { id: 'member-sign-up', enabled: true, required: false, name: 'Member Sign Up wizard' },
      { id: 'reservation-tablet', enabled: true, required: false, name: 'Reservation Tablet wizard' },
      { id: 'book-it-now', enabled: true, required: false, name: 'Book It Now wizard' },

      // Integrations
      { id: 'appointment-plus', enabled: false, required: false, name: 'AppointmentPlus integration' },
      { id: 'quick-books', enabled: false, required: false, name: 'QuickBooks integration' },
      { id: 'teamgo', enabled: false, required: false, name: 'Teamgo integration' },
      { id: 'kisi', enabled: false, required: false, name: 'Kisi integration' },
      { id: 'doorflow', enabled: false, required: false, name: 'DoorFlow integration' },
      { id: 'ezeep', enabled: false, required: false, name: 'Ezeep integration' },
      { id: 'greetly', enabled: false, required: false, name: 'Greetly integration' },
      { id: 'affiliatly', enabled: false, required: false, name: 'Affiliatly integration' },
    ];

    const defaults = bundles
      .reduce((result, bundle) => { result[bundle.id] = bundle.enabled; return result; }, {});

    return {
      enable: id => { defaults[id] = true },
      disable: id => { defaults[id] = false },
      $get: ($resource, autoCacheFactory, deskworksConfig) => {
        const cache = autoCacheFactory('bundles-config', 120);
        const res = $resource(deskworksConfig.getApiAbsUrl('/white-label/bundles'), {}, {
          get: { method: 'GET', cache: cache },
          update: { method: 'PUT' },
        });

        return {
          enable: id => { defaults[id] = true },
          disable: id => { defaults[id] = false },
          getOptionalBundles: () => bundles.filter(b => !b.required),

          get: () => res.get().$promise
            .then(config => angular.extend({}, defaults, config)),

          save: config => res.update({}, config).$promise
              .finally(() => cache.removeAll()),
        };
      },
    };
  });
