import lazyLoaders from 'bundles/lazy-loaders.service.js';
import forbid from 'shared/forbid/forbid.directive';

export default angular.module('deskworks.buyPass', [
  lazyLoaders.name,
  forbid.name,
  'deskworks.PassProduct.service',
  'deskworks.setup.service',
])
  .config(function($routeProvider) {
    const authUrl = '/buy-pass/user';

    /**
     * Buy Pass -> Select A Pass
     */
    $routeProvider.when('/buy-pass', {
      template: '<forbid admin="true"><div><ng-include src="\'steps/01-select-pass.tpl.html\'"></ng-include></div></forbid>',
      controller: 'BuyPassSelectPassCtrl as $ctrl',
      routeBranch: 'buy-pass',
      hideSidebar: true,
      userTasksPolicy: false,
      reloadOnSearch: false,
      translations: ['buy-pass'],
      resolve: {
        reg: () => { return {}; },
        passes: (state, dwAlerts, PassProduct) => {
          if (!state.getCurrentCenterId()) return dwAlerts.error(null, 'Center is not selected.');
          return PassProduct.query(state.getCurrentCenterId())
            .catch(err => dwAlerts.error(err, 'Failed to query pass products.'));
        },
        center: (state, dwAlerts, centerService) => centerService.get(state.getCurrentCenterId())
          .catch(err => dwAlerts.error(err, 'Failed to get center.')),
        lazy: 'lazyLoadBuyPass',
      }
    });

    /**
     * Sign-In
     */
    $routeProvider.when('/buy-pass/user', {
      template: '<forbid admin="true"><div><dw-sign-in id="signIn" show-sign-up="true"></dw-sign-in></div></forbid>',
      controller: 'BuyPassSignInCtrl as vm',
      routeBranch: 'buy-pass',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['sign-in'],
      resolve: {
        redirect: ($q, state) => {
          if (state.getLoggedUserId()) { // redirect logged user to Buy Pass -> User Profile
            state.setPath(state.getPath() + '/' + state.getLoggedUserId(), true);
            return $q.reject();
          }
        },
        lazy: 'lazyLoadBuyPass',
      }
    });

    /**
     * User Profile
     */
    $routeProvider.when('/buy-pass/user/:userId', {
      template: '<forbid admin="true"><div><ng-include src="\'templates/new-user-profile.tpl.html\'"></ng-include></div></forbid>',
      controller: 'BuyPassUserProfileCtrl as vm',
      routeBranch: 'buy-pass',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['user-profile'],
      resolve: {
        reg: $route => { return { member: false, selfRegistration: true, new: $route.current.params.userId === 'new' }; },
        userProfile: ($q, $route, helper, state, routeHelper, dwAlerts, userProfileService, centerService, resolveTimezone) => {
          if ($route.current.params.userId === 'new') {
            return centerService.get(state.getCurrentCenterId()).then(
              center => userProfileService.new(center, 'nonMember', 'signUp')
                .catch(err => dwAlerts.error(err, 'Failed to get new user profile template.'))
            );
          } else {
            return $q.all([routeHelper.auth(authUrl), resolveTimezone()])
              .then(() => userProfileService.getForRegistration(state.getCurrentCenterId(), $route.current.params.userId, 'nonMember', 'signUp')
                .catch(err => dwAlerts.error(err, 'Failed to get user profile.')));
          }
        },
        center: (state, dwAlerts, centerService) => centerService.get(state.getCurrentCenterId())
          .catch(err => dwAlerts.error(err, 'Failed to get center.')),
        lazy: 'lazyLoadBuyPass',
      },
    });

    /**
     * Purchase
     */
    $routeProvider.when('/buy-pass/user/:userId/purchase/:purchaseId', {
      template: '<forbid admin="true"><div><ng-include src="\'steps/04-purchase.tpl.html\'"></ng-include></div></forbid>',
      controller: 'BuyPassPurchaseCtrl',
      controllerAs: '$ctrl',
      routeBranch: 'buy-pass',
      hideSidebar: true,
      userTasksPolicy: false,
      reloadOnSearch: false,
      translations: ['buy-pass', 'purchase', 'billing-profile', 'user-profile', 'simple-reservation'],
      resolve: {
        reg: () => { return { member: false, selfRegistration: true }; },
        center: (state, dwAlerts, centerService) => centerService.get(state.getCurrentCenterId())
          .catch(err => dwAlerts.error(err, 'Failed to get center.')),
        purchase: ($q, $route, dwAlerts, state, routeHelper, purchaseService, resolveTimezone) =>
          $q.all([routeHelper.auth(authUrl), resolveTimezone()])
            .then(() => purchaseService.get(state.getCurrentCenterId(), $route.current.params.userId, $route.current.params.purchaseId)
              .catch(err => dwAlerts.error(err, 'Failed to get purchase.'))
          ),
        user: ($q, $route, dwAlerts, state, routeHelper, userProfileService, resolveTimezone) =>
          $q.all([routeHelper.auth(authUrl), resolveTimezone()])
            .then(() => userProfileService.get(state.getCurrentCenterId(), $route.current.params.userId)
              .catch(err => dwAlerts.error(err, 'Failed to get user profile.'))),
        settings: (dwAlerts, setupService) => setupService.getSetupSettings()
          .catch(err => dwAlerts.error(err, 'Failed to get setup settings.')),
        lazy: 'lazyLoadBuyPass',
      }
    });

    /**
     * Staff Help
     */
    $routeProvider.when('/buy-pass/user/:userId/purchase/:purchaseId/staff-help', {
      template: '<forbid admin="true"><div><ng-include src="\'staff-help/staff-help.tpl.html\'"></ng-include></div></forbid>',
      controller: 'BuyPassStaffHelpCtrl as vm',
      routeBranch: 'buy-pass',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['simple-reservation'],
      resolve: {
        auth: routeHelper => routeHelper.auth(authUrl),
        lazy: 'lazyLoadBuyPass',
      }
    });

    /**
     * Complete
     */
    $routeProvider.when('/buy-pass/user/:userId/purchase/:purchaseId/complete', {
      template: '<forbid admin="true"><div><ng-include src="\'steps/06-complete.tpl.html\'"></ng-include></div></forbid>',
      controller: 'BuyPassCompleteCtrl as $ctrl',
      routeBranch: 'buy-pass',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['buy-pass'],
      resolve: {
        auth: routeHelper => routeHelper.auth(authUrl),
        lazy: 'lazyLoadBuyPass',
      }
    });
  });
