import 'bundles/bundles-config.service.js';

export default angular.module('deskworks.lazyLoaders', ['deskworks.dwAlerts', 'deskworks.bundlesConfig'])

  .factory('checkBundleConfig', ($injector, bundlesConfig, dwAlerts) => {
    return (id, silent = false) => bundlesConfig.get()
      .catch(err => dwAlerts.error(err, 'Failed to load bundles config.'))
      .then(config => {
        if (!config[id] && silent) throw null;
        if (!config[id]) return dwAlerts.error(`${id} bundle is disabled.`);
        return true;
      });
  })

