import 'bundles/lazy-loaders.service.js';

export default angular.module('deskworks.invoice', [
  'oc.lazyLoad',
  'deskworks.purchase.service',
  'deskworks.lazyLoaders',
])
  .config(function($routeProvider) {

    $routeProvider.when('/invoice/:orderId', {
      template: '<div ng-include="\'invoice.tpl.html\'"></div>',
      controller: 'InvoiceCtrl as $ctrl',
      routeBranch: 'invoice',
      reloadOnSearch: false,
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['invoice', 'reports'],
      resolve: {
        auth: (routeHelper) => routeHelper.selectUser(),

        orderId: ($route) => $route.current.params.orderId,

        lazy: 'lazyLoadInvoice',
      }
    });

    $routeProvider.when('/purchases/:purchaseId/view', { // Post Payments or Charges -> View as PDF
      template: '<div ng-include="\'invoice.tpl.html\'"></div>',
      controller: 'InvoiceCtrl as $ctrl',
      routeBranch: 'purchases',
      reloadOnSearch: false,
      translations: ['invoice', 'reports'],
      resolve: {
        orderId: ($route) => $route.current.params.purchaseId,

        back: (state) => () => {
          state.resetParams();
          return state.setPath('/purchases');
        },

        preventOrderUserMismatch: ($route, $q, dwAlerts, routeHelper, state, helper, purchaseService) =>
          routeHelper.auth()
            .then(() => routeHelper.selectUser())
            .then(() => purchaseService.query(state.getCurrentCenterId(), state.getCurrentUserId())
              .catch(err => dwAlerts.error(err, 'Failed to query purchases.')))
            .then(orders => {
              if (!orders.find(o => +o.id === +$route.current.params.purchaseId)) {
                state.resetParams();
                state.setPath('/purchases'); // go to purchases when user<->order mismatch
                return $q.reject();
              }
            }),

        lazy: 'lazyLoadInvoice',
      }
    });

    $routeProvider.when('/generate-invoice', {
      template: '<div ng-include="\'invoice.tpl.html\'"></div>',
      controller: 'InvoiceCtrl as $ctrl',
      routeBranch: 'generate-invoice',
      reloadOnSearch: false,
      translations: ['invoice', 'reports'],
      resolve: {
        auth: (routeHelper) => routeHelper.auth()
          .then(() => routeHelper.selectUser()),

        orderId: () => null,

        lazy: 'lazyLoadInvoice',
      }
    });
  });
