import 'bundles/lazy-loaders.service.js';
import ReservationUnit from 'shared/services/inventory/reservation-unit.service';
import ReservationField from 'shared/services/setup/reservation-field.service';

export default angular.module('deskworks.simpleReservation', [
  'ngRoute',
  'deskworks.center.service',
  'deskworks.reservationCategory.service',
  'deskworks.calendarService',
  'deskworks.userProfile.service',
  'deskworks.purchase.service',
  'deskworks.setup.service',
  'deskworks.lazyLoaders',
  ReservationUnit.name,
  ReservationField.name,
])
  .config(function($routeProvider) {

    // Simple Reservation Auth helper
    function srAuth($route, helper, routeHelper) {
      const url = '/simple-reservation/centers/:centerId/types/:reservationTypeId/dates/:date/units/:reservationUnitId/users';
      return routeHelper.auth(helper.interpolateRoute(url, $route.current.params));
    }

    /**
     * 01. Centers List
     */
    $routeProvider.when('/simple-reservation', {
      template: '<div ng-include="\'01-centers/centers.tpl.html\'"></div>',
      controller: 'SimpleReservationCentersCtrl as vm',
      routeBranch: 'new-reservation',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['simple-reservation'],
      resolve: {
        centers: ($q, dwAlerts, centerService) => {
          return centerService.getCenters()
            .catch(err => dwAlerts.error(err, 'Failed to query centers.'))
            .then(centers => {
              centers = centers.filter(center => center.reservationUnitsCount > 0);
              if (!centers.length) return dwAlerts.error('<b>No public centers.</b> At least one center needs <b>Center for Internal Use</b> setting <b>turned off.</b>');
              return centers;
            });
        },
        lazy: 'lazyLoadSimpleReservation',
      }
    });

    /**
     * 02. Reservation Categories List
     */
    $routeProvider.when('/simple-reservation/centers/:centerId/types', {
      template: '<div ng-include="\'02-reservation-categories/reservation-categories.tpl.html\'"></div>',
      controller: 'SimpleReservationCategoriesCtrl as vm',
      routeBranch: 'new-reservation',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['simple-reservation', 'reservations'],
      resolve: {
        reservationCategories: ($route, state, helper, dwAlerts, calendarService, resolveTimezone) => {
          return resolveTimezone()
            .then(() => calendarService.query($route.current.params.centerId, moment(), moment())
              .catch(err => dwAlerts.error(err, 'Failed to query reservation categories.')));
        },
        center: ($route, dwAlerts, centerService) => {
          return centerService.get($route.current.params.centerId)
            .catch(err => dwAlerts.error(err, 'Failed to get center.'));
        },
        settings: (dwAlerts, setupService) => {
          return setupService.getSetupSettings()
            .catch(err => dwAlerts.error(err, 'Failed to get setup settings.'));
        },
        lazy: 'lazyLoadSimpleReservation',
      }
    });

    /**
     * 03. Reservation Category View
     */
    $routeProvider.when('/simple-reservation/centers/:centerId/types/:reservationTypeId', {
      template: '<div ng-include="\'03-reservation-category/reservation-category.tpl.html\'"></div>',
      controller: 'SimpleReservationCategoryCtrl as vm',
      routeBranch: 'new-reservation',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['simple-reservation'],
      resolve: {
        reservationCategory: ($q, $route, dwAlerts, reservationCategoryService) => {
          return reservationCategoryService.get($route.current.params.centerId, $route.current.params.reservationTypeId)
            .catch(err => dwAlerts.error(err, 'Failed to get reservation category.'));
        },
        lazy: 'lazyLoadSimpleReservation',
      },
    });

    /**
     * 04. Date
     */
    $routeProvider.when('/simple-reservation/centers/:centerId/types/:reservationTypeId/dates', {
      template: '<div ng-include="\'04-date/date.tpl.html\'"></div>',
      controller: 'SimpleReservationDateCtrl as vm',
      routeBranch: 'new-reservation',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['simple-reservation'],
      resolve: {
        unavailableDates: ($route, dwAlerts, reservationCategoryService) => {
          return reservationCategoryService.getUnavailableDates($route.current.params.centerId, $route.current.params.reservationTypeId)
            .catch(err => dwAlerts.error(err, 'Failed to query unavailable dates.'));
        },
        lazy: 'lazyLoadSimpleReservation',
      }
    });

    /**
     * 05. Reservation Units
     */
    $routeProvider.when('/simple-reservation/centers/:centerId/types/:reservationTypeId/dates/:date/units', {
      template: '<div ng-include="\'05-reservation-units/reservation-units.tpl.html\'"></div>',
      controller: 'SimpleReservationUnitsCtrl as vm',
      routeBranch: 'new-reservation',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['simple-reservation', 'reservations'],
      resolve: {
        resUnits: ($route, $filter, state, helper, dwAlerts, calendarService, resolveTimezone) => {
          return resolveTimezone()
            .then(() => {
              let date = $filter('toDate')($route.current.params.date);
              date = moment.isMoment(date) ? date : moment();
              return calendarService.query($route.current.params.centerId, date, date)
                .catch(err => dwAlerts.error(err, 'Failed to fetch calendar data.'))
                .then(resTypes => {
                  return (helper.find(resTypes, $route.current.params.reservationTypeId) || {}).reservationUnits || [];
                });
            });
        },
        center: ($route, dwAlerts, centerService) => {
          return centerService.get($route.current.params.centerId)
            .catch(err => dwAlerts.error(err, 'Failed to get center.'));
        },
        settings: (dwAlerts, setupService) => {
          return setupService.getSetupSettings()
            .catch(err => dwAlerts.error(err, 'Failed to get setup settings.'));
        },
        lazy: 'lazyLoadSimpleReservation',
      }
    });

    /**
     * 06. Time
     */
    $routeProvider.when('/simple-reservation/centers/:centerId/types/:reservationTypeId/dates/:date/units/:reservationUnitId', {
      template: '<div ng-include="\'06-time/simple-reservation-time.tpl.html\'"></div>',
      controller: 'SimpleReservationTimeCtrl as $ctrl',
      reloadOnSearch: false,
      routeBranch: 'new-reservation',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['simple-reservation', 'reservations'],
      resolve: {
        center: ($route, dwAlerts, centerService) => {
          return centerService.get($route.current.params.centerId)
            .catch(err => dwAlerts.error(err, 'Failed to get center.'));
        },
        calData: ($route, $filter, state, helper, dwAlerts, calendarService, resolveTimezone) => {
          return resolveTimezone()
            .then(() => {
              let date = $filter('toDate')($route.current.params.date || state.getParam('date'));
              date = moment.isMoment(date) ? date : moment();
              return calendarService.query($route.current.params.centerId, date, date, state.getParam('role'))
                .catch(err => dwAlerts.error(err, 'Failed to fetch calendar data.'));
            });
        },
        settings: (dwAlerts, setupService) => {
          return setupService.getSetupSettings()
            .catch(err => dwAlerts.error(err, 'Failed to get setup settings.'));
        },
        reservationFields: (dwAlerts, ReservationField, session) => session.wait().catch(angular.noop)
          .then(() => ReservationField.query()
            .catch(err => dwAlerts.error(err, 'Failed to query reservation fields.'))),
        lazy: 'lazyLoadSimpleReservation',
      }
    });

    /**
     * 06.5. Sign-In
     */
    $routeProvider.when('/simple-reservation/centers/:centerId/types/:reservationTypeId/dates/:date/units/:reservationUnitId/users', {
      template: '<div ng-include="\'other/simple-reservation-sign-in.tpl.html\'"></div>',
      controller: 'SimpleReservationSignInCtrl as vm',
      routeBranch: 'new-reservation',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['sign-in'],
      resolve: {
        redirect: ($q, state) => {
          if (state.isLoggedNonMember()) { // Redirect non-member to Simple Reservation / User Profile
            state.setPath(state.getPath() + '/' + state.getLoggedUserId(), true);
            return $q.reject();
          }
          if (state.getLoggedUserId()) { // Redirect member/coordinator/admin to Simple Reservation / Confirm Reservation
            state.setPath(state.getPath() + '/' + state.getLoggedUserId() + '/confirm', true);
            return $q.reject();
          }
        },
        lazy: 'lazyLoadSimpleReservation',
      }
    });

    /**
     * 07. User Profile
     */
    $routeProvider.when('/simple-reservation/centers/:centerId/types/:reservationTypeId/dates/:date/units/:reservationUnitId/users/:userId', {
      template: '<div ng-include="\'templates/new-user-profile.tpl.html\'"></div>',
      controller: 'SimpleReservationUserProfileCtrl as vm',
      routeBranch: 'new-reservation',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['user-profile'],
      resolve: {
        reg: $route => {
          return { member: false, selfRegistration: true, new: $route.current.params.userId === 'new' };
        },
        userProfile: (dwAlerts, $route, helper, state, routeHelper, userProfileService, centerService) => {
          if ($route.current.params.userId === 'new') {
            return centerService.get($route.current.params.centerId)
              .then(center => userProfileService.new(center, 'nonMember', 'signUp')
                  .catch(err => dwAlerts.error(err, 'Failed to get new member profile template.')));
          } else {
            return srAuth($route, helper, routeHelper)
              .then(() => userProfileService.getForRegistration($route.current.params.centerId, $route.current.params.userId, 'nonMember', 'signUp')
                .catch(err => dwAlerts.error(err, 'Failed to get user profile.')));
          }
        },
        lazy: 'lazyLoadSimpleReservation',
      }
    });

    /**
     * Confirm Reservation (for Member)
     */
    $routeProvider.when('/simple-reservation/centers/:centerId/types/:reservationTypeId/dates/:date/units/:reservationUnitId/users/:userId/confirm', {
      template: '<div ng-include="\'other/confirm.tpl.html\'"></div>',
      controller: 'SimpleReservationConfirmCtrl',
      controllerAs: 'vm',
      routeBranch: 'new-reservation',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['simple-reservation', 'reservations'],
      resolve: {
        center: ($route, dwAlerts, centerService) => {
          return centerService.get($route.current.params.centerId)
            .catch(err => dwAlerts.error(err, 'Failed to get center.'));
        },
        reservationUnit: function($route, dwAlerts, helper, routeHelper, ReservationUnit) {
          return srAuth($route, helper, routeHelper)
            .then(() => ReservationUnit.get($route.current.params.centerId, $route.current.params.reservationUnitId)
              .catch(err => dwAlerts.error(err, 'Failed to get reservation unit.')));
        },
        settings: (dwAlerts, setupService) => {
          return setupService.getSetupSettings()
            .catch(err => dwAlerts.error(err, 'Failed to get setup settings.'));
        },
        reservationFields: (dwAlerts, ReservationField, session) => session.wait().catch(angular.noop)
          .then(() => ReservationField.query()
            .catch(err => dwAlerts.error(err, 'Failed to query reservation fields.'))),
        lazy: 'lazyLoadSimpleReservation',
      }
    });

    /**
     * 08. Purchase
     */
    $routeProvider.when('/simple-reservation/centers/:centerId/types/:reservationTypeId/dates/:date/units/:reservationUnitId/users/:userId/purchases/:purchaseId', {
      template: '<div ng-include="\'08-purchase/purchase.tpl.html\'"></div>',
      controller: 'SimpleReservationPurchaseCtrl',
      controllerAs: 'vm',
      routeBranch: 'new-reservation',
      hideSidebar: true,
      userTasksPolicy: false,
      reloadOnSearch: false,
      translations: ['simple-reservation', 'reservations', 'purchase', 'billing-profile'],
      resolve: {
        reg: () => { return { member: false, selfRegistration: true }; },
        center: ($route, dwAlerts, centerService) => {
          return centerService.get($route.current.params.centerId)
            .catch(err => dwAlerts.error(err, 'Failed to get center.'));
        },
        purchase: ($route, dwAlerts, helper, routeHelper, purchaseService, resolveTimezone) => {
          return srAuth($route, helper, routeHelper)
            .then(() => resolveTimezone())
            .then(() => purchaseService.get($route.current.params.centerId, $route.current.params.userId, $route.current.params.purchaseId)
              .catch(err => dwAlerts.error(err, 'Failed to get purchase.')));
        },
        reservationUnit: function($route, dwAlerts, ReservationUnit) {
          return ReservationUnit.get($route.current.params.centerId, $route.current.params.reservationUnitId)
            .catch(err => dwAlerts.error(err, 'Failed to get reservation unit.'));
        },
        userProfile: ($q, $route, dwAlerts, helper, state, routeHelper, userProfileService, resolveTimezone) => {
          return srAuth($route, helper, routeHelper)
            .then(() => resolveTimezone())
            .then(() => userProfileService.get($route.current.params.centerId, $route.current.params.userId)
              .catch(err => dwAlerts.error(err, 'Failed to get user profile.')));
        },
        settings: (dwAlerts, setupService) => {
          return setupService.getSetupSettings()
            .catch(err => dwAlerts.error(err, 'Failed to get setup settings.'));
        },
        reservationFields: (dwAlerts, ReservationField, session) => session.wait().catch(angular.noop)
          .then(() => ReservationField.query()
            .catch(err => dwAlerts.error(err, 'Failed to query reservation fields.'))),
        lazy: 'lazyLoadSimpleReservation',
      }
    });

    /**
     * Staff Help
     */
    $routeProvider.when('/simple-reservation/centers/:centerId/types/:reservationTypeId/dates/:date/units/:reservationUnitId/users/:userId/purchases/:purchaseId/staff-help', {
      template: '<div ng-include="\'staff-help/staff-help.tpl.html\'"></div>',
      controller: 'StaffHelpCtrl',
      controllerAs: 'vm',
      routeBranch: 'new-reservation',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['simple-reservation'],
      resolve: {
        user: ($route, helper, routeHelper) => {
          return srAuth($route, helper, routeHelper);
        },
        lazy: 'lazyLoadSimpleReservation',
      }
    });

    /**
     * Complete
     */
    $routeProvider.when('/simple-reservation/centers/:centerId/types/:reservationTypeId/dates/:date/units/:reservationUnitId/users/:userId/purchases/:purchaseId/complete', {
      template: '<div ng-include="\'other/complete.tpl.html\'"></div>',
      controller: 'SimpleReservationCompleteCtrl',
      routeBranch: 'new-reservation',
      controllerAs: 'vm',
      hideSidebar: true,
      translations: ['simple-reservation', 'purchase'],
      userTasksPolicy: false,
      resolve: {
        reg: () => { return { member: false, selfRegistration: true }; },
        purchase: ($route, dwAlerts, state, helper, routeHelper, purchaseService, resolveTimezone) => {
          return srAuth($route, helper, routeHelper)
            .then(() => resolveTimezone())
            .then(() => purchaseService.get($route.current.params.centerId, $route.current.params.userId, $route.current.params.purchaseId)
              .catch(err => dwAlerts.error(err, 'Failed to get purchase.')));
        },
        center: ($route, dwAlerts, centerService) => {
          return centerService.get($route.current.params.centerId)
            .catch(err => dwAlerts.error(err, 'Failed to get center.'));
        },
        settings: (dwAlerts, setupService) => {
          return setupService.getSetupSettings()
            .catch(err => dwAlerts.error(err, 'Failed to get setup settings.'));
        },
        lazy: 'lazyLoadSimpleReservation',
      }
    });

    $routeProvider.when('/simple-reservation/complete', {
      template: '<div ng-include="\'other/complete.tpl.html\'"></div>',
      controller: 'SimpleReservationCompleteCtrl as vm',
      routeBranch: 'new-reservation',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['simple-reservation'],
      resolve: {
        purchase: function() {},
        center: function() {},
        auth: ($route, helper, routeHelper) => {
          return srAuth($route, helper, routeHelper);
        },
        settings: (dwAlerts, setupService) => {
          return setupService.getSetupSettings()
            .catch(err => dwAlerts.error(err, 'Failed to get setup settings.'));
        },
        lazy: 'lazyLoadSimpleReservation',
      }
    });
  });
