import 'bundles/lazy-loaders.service.js';
import forbid from 'shared/forbid/forbid.directive';

export default angular.module('deskworks.memberSignUp', [
  forbid.name,
  'deskworks.lazyLoaders',
  'deskworks.membership.service',
])

  .config(function($routeProvider) {
    const stepCount = 3;

    /**
     * 01. New Membership
     */
    $routeProvider.when('/member-sign-up', {
      template: '<forbid admin="true" member="$resolve.user"><div><ng-include src="\'steps/01-membership.tpl.html\'"></ng-include></div></forbid>',
      controller: 'MemberSignUpMembershipCtrl as vm',
      label: 'New Membership',
      routeBranch: 'member-sign-up',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['member-sign-up'],
      resolve: {
        reg: () => { return { member: true, selfRegistration: true, step: 1, stepCount }; },
        data: ($q, dwAlerts, centerService, setupService, state, filterHiddenCenters) => {
          return $q.all({
            centers: centerService.getCenters()
              .catch(err => dwAlerts.error(err, 'Failed to query centers.'))
              .then(filterHiddenCenters),
            settings: setupService.getSetupSettings()
              .catch(err => dwAlerts.error(err, 'Failed to get setup settings.')),
          })
            .then(data => {
              if (!data.centers.length) {
                return dwAlerts.error('<b>No public centers.</b> At least one center needs <b>Center for Internal Use</b> setting <b>turned off.</b>');
              }
              if (data.settings.skipPlanMemberSignUp && data.centers.length === 1) {
                state.setCurrentCenterId(data.centers[0].id); // auto-select center and redirect to the next step
                return state.setPath(state.getPath() + '/' + (state.getCurrentUserId() || 'new'));
              }
              return data;
            });
        },
        user: (session, state, userProfileService) => session.wait()
          .then(() => session.isRole('member') ? userProfileService.get(state.getCurrentCenterId(), session.user.id) : null),
        lazy1: 'lazyMemberSignUp',
      }
    });

    /**
     * 01. New membership (landing page)
     */
    $routeProvider.when('/member-sign-up-landing', {
      template: '<forbid admin="true" member="$resolve.user"><div><ng-include src="\'steps/01-landing.tpl.html\'"></ng-include></div></forbid>',
      controller: 'MemberSignUpLandingCtrl as $ctrl',
      label: 'New Membership',
      routeBranch: 'member-sign-up',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['member-sign-up'],
      resolve: {
        reg: () => { return { member: true, selfRegistration: true, step: 1, stepCount: stepCount }; },
        center: (dwAlerts, centerService, state) => centerService.get(state.getCurrentCenterId())
          .catch(err => dwAlerts.error(err, 'Failed to get a center.')),
        product: (dwAlerts, membershipService, state) => membershipService.query(state.getCurrentCenterId())
          .catch(err => dwAlerts.error(err, 'Failed to query membership categories.'))
          .then(categories => {
            const productId = +state.getParam('productId');
            let product;
            categories.some(category => category.products.some(prod => { if (prod.id === productId) { product = prod; return true; } }));
            if (!product) return dwAlerts.error('No product with id=' + state.getParam('productId') + ' found in membership categories.');
            return product;
          }),
        user: (session, state, userProfileService) => session.wait()
          .then(() => session.isRole('member') ? userProfileService.get(state.getCurrentCenterId(), session.user.id) : null),
        lazy1: 'lazyMemberSignUp',
      }
    });

    /**
     * 02. New User Profile
     */
    $routeProvider.when('/member-sign-up/new', {
      template: '<forbid admin="true" member="$resolve.user"><div><ng-include src="\'templates/new-user-profile.tpl.html\'"></ng-include></div></forbid>',
      controller: 'MemberSignUpProfileCtrl',
      controllerAs: 'vm',
      label: 'Member Sign Up',
      routeBranch: 'member-sign-up',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['user-profile', 'member-sign-up'],
      resolve: {
        reg: ($q, helper, state, dwAlerts, centerService, setupService, filterHiddenCenters) => {
          return $q.all({
            centers: centerService.getCenters()
              .catch(err => dwAlerts.error(err, 'Failed to query centers.'))
              .then(filterHiddenCenters),
            settings: setupService.getSetupSettings()
              .catch(err => dwAlerts.error(err, 'Failed to get setup settings.'))
          })
            .then(data => {
              if (!data.centers.length) {
                return dwAlerts.error('<b>No public centers.</b> At least one center needs <b>Center for Internal Use</b> setting <b>turned off.</b>');
              }
              if (data.settings.skipPlanMemberSignUp && data.centers.length === 1) {
                return {
                  member: true, selfRegistration: true, step: 1, stepCount: stepCount - 1, new: true,
                  phone: (helper.find(data.centers, state.getCurrentCenterId()) || {}).phone,
                };
              }
              return {
                member: true, selfRegistration: true, step: 2, stepCount, new: true,
                phone: (helper.find(data.centers, state.getCurrentCenterId()) || {}).phone,
              };
            });
        },
        userProfile: (dwAlerts, userProfileService, helper, state, centerService) => {
          return centerService.getCenter(state.getCurrentCenterId())
            .catch(err => dwAlerts.error(err, 'Failed to get center.'))
            .then(center => {
              return userProfileService.new(center, 'member', 'signUp')
                .catch(err => dwAlerts.error(err, 'Failed to get new member profile template.'));
            });
        },
        user: (session, state, userProfileService) => session.wait()
          .then(() => session.isRole('member') ? userProfileService.get(state.getCurrentCenterId(), session.user.id) : null),
        settings: (dwAlerts, setupService) => {
          return setupService.getSetupSettings()
            .catch(err => dwAlerts.error(err, 'Failed to get setup settings.'));
        },
        lazy1: 'lazyMemberSignUp',
      }
    });

    /**
     * 02. Existing User Profile
     */
    $routeProvider.when('/member-sign-up/:userId', {
      template: '<forbid admin="true" member="$resolve.userProfile"><div><ng-include src="\'templates/new-user-profile.tpl.html\'"></ng-include></div></forbid>',
      controller: 'MemberSignUpProfileCtrl',
      controllerAs: 'vm',
      label: 'Member Sign Up',
      routeBranch: 'member-sign-up',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['user-profile', 'member-sign-up'],
      resolve: {
        reg: ($q, helper, state, dwAlerts, centerService, setupService, filterHiddenCenters) => {
          return $q.all({
            centers: centerService.getCenters()
              .catch(err => dwAlerts.error(err, 'Failed to query centers.'))
              .then(filterHiddenCenters),
            settings: setupService.getSetupSettings()
              .catch(err => dwAlerts.error(err, 'Failed to get setup settings.'))
          })
            .then(data => {
              if (!data.centers.length) {
                return dwAlerts.error('<b>No public centers.</b> At least one center needs <b>Center for Internal Use</b> setting <b>turned off.</b>');
              }
              if (data.settings.skipPlanMemberSignUp && data.centers.length === 1) {
                return {
                  member: true, selfRegistration: true, step: 1, stepCount: stepCount - 1,
                  phone: (helper.find(data.centers, state.getCurrentCenterId()) || {}).phone,
                };
              }
              return {
                member: true, selfRegistration: true, step: 2, stepCount,
                phone: (helper.find(data.centers, state.getCurrentCenterId()) || {}).phone,
              };
            });
        },
        userProfile: (dwAlerts, $route, routeHelper, userProfileService, resolveTimezone) => {
          return routeHelper.auth()
            .then(() => resolveTimezone())
            .then(() => userProfileService.getForRegistration(0, $route.current.params.userId, 'member', 'signUp')
              .catch(err => dwAlerts.error(err, 'Failed to get user profile.')));
        },
        settings: (dwAlerts, setupService) => {
          return setupService.getSetupSettings()
            .catch(err => dwAlerts.error(err, 'Failed to get setup settings.'));
        },
        lazy1: 'lazyMemberSignUp',
      }
    });

    /**
     * 03. Purchase
     */
    $routeProvider.when('/member-sign-up/:userId/purchases/:purchaseId', {
      template: '<forbid admin="true" member="$resolve.userProfile"><div><ng-include src="\'steps/03-purchase.tpl.html\'"></ng-include></div></forbid>',
      controller: 'MemberSignUpPurchaseCtrl',
      controllerAs: 'vm',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['member-sign-up', 'purchase', 'billing-profile', 'simple-reservation'],
      resolve: {
        reg: () => { return { member: true, selfRegistration: true, step: 3, stepCount }; },
        center: (dwAlerts, state, centerService) => {
          return centerService.get(state.getCurrentCenterId())
            .catch(err => dwAlerts.error(err, 'Failed to get center.'));
        },
        purchase: ($route, dwAlerts, state, routeHelper, purchaseService, resolveTimezone) => {
          return routeHelper.auth()
            .then(() => resolveTimezone())
            .then(() => purchaseService.get(state.getCurrentCenterId(), $route.current.params.userId, $route.current.params.purchaseId, state.getParam('productId'))
              .catch(err => dwAlerts.error(err, 'Failed to get purchase.')));
        },
        userProfile: ($q, $route, dwAlerts, state, routeHelper, userProfileService, resolveTimezone) => {
          return routeHelper.auth()
            .then(() => resolveTimezone())
            .then(() => userProfileService.get(state.getCurrentCenterId(), $route.current.params.userId)
              .catch(err => dwAlerts.error(err, 'Failed to get user profile.')));
        },
        settings: (dwAlerts, setupService) => {
          return setupService.getSetupSettings()
            .catch(err => dwAlerts.error(err, 'Failed to get setup settings.'));
        },
        lazy1: 'lazyMemberSignUp',
        lazy2: 'lazyLoadAffiliatly',
      }
    });

    /**
     * Staff Help
     */
    $routeProvider.when('/member-sign-up/:userId/purchases/:purchaseId/staff-help', {
      template: '<forbid admin="true"><div><ng-include src="\'staff-help/staff-help.tpl.html\'"></ng-include></div></forbid>',
      controller: 'StaffHelpCtrl',
      controllerAs: 'vm',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['simple-reservation'],
      resolve: {
        auth: routeHelper => routeHelper.auth(),
        lazy1: 'lazyMemberSignUp',
      }
    });

    /**
     * 04. Complete
     */
    $routeProvider.when('/member-sign-up/:userId/purchases/:purchaseId/complete', {
      template: '<forbid admin="true"><div><ng-include src="\'steps/04-complete.tpl.html\'"></ng-include></div></forbid>',
      controller: 'MemberSignUpCompleteCtrl',
      controllerAs: 'vm',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['member-sign-up', 'purchase', 'simple-reservation'],
      resolve: {
        reg: () => { return { member: true, selfRegistration: true }; },
        purchase: ($route, dwAlerts, routeHelper, state, purchaseService, resolveTimezone) => {
          return routeHelper.auth()
            .then(() => resolveTimezone())
            .then(() => purchaseService.get(state.getCurrentCenterId(), $route.current.params.userId, $route.current.params.purchaseId)
              .catch(err => dwAlerts.error(err, 'Failed to get purchase.')));
        },
        center: (state, dwAlerts, centerService) => {
          return centerService.get(state.getCurrentCenterId())
            .catch(err => dwAlerts.error(err, 'Failed to get center.'));
        },
        template: (state, dwAlerts, Notifications) => {
          return Notifications.getEmailTemplate('receipt-page')
            .catch(err => dwAlerts.error(err, 'Failed to get receipt page settings.'));
        },
        lazy1: 'lazyMemberSignUp',
      }
    });

    /**
     * No Plan -> Billing
     */
    $routeProvider.when('/member-sign-up/:userId/billing', {
      template: '<forbid admin="true" member="$resolve.userProfile"><div><ng-include src="\'templates/new-billing-profile.tpl.html\'"></ng-include></div></forbid>',
      controller: 'MemberSignUpNoPlanBillingCtrl',
      controllerAs: 'vm',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['member-sign-up', 'billing-profile', 'user-profile'],
      resolve: {
        reg: ($q, helper, state, dwAlerts, centerService, setupService, filterHiddenCenters) => {
          return $q.all({
            centers: centerService.getCenters()
              .catch(err => dwAlerts.error(err, 'Failed to query centers.'))
              .then(filterHiddenCenters),
            settings: setupService.getSetupSettings()
              .catch(err => dwAlerts.error(err, 'Failed to get setup settings.'))
          })
            .then(data => {
              if (!data.centers.length) {
                return dwAlerts.error('<b>No public centers.</b> At least one center needs <b>Center for Internal Use</b> setting <b>turned off.</b>');
              }
              if (data.settings.skipPlanMemberSignUp && data.centers.length === 1) {
                return {
                  member: true, selfRegistration: true, step: 2, stepCount: stepCount - 2,
                  phone: (helper.find(data.centers, state.getCurrentCenterId()) || {}).phone,
                };
              }
              return {
                member: true, selfRegistration: true, step: 3, stepCount: stepCount - 1,
                phone: (helper.find(data.centers, state.getCurrentCenterId()) || {}).phone,
              };
            });
        },
        userProfile: ($route, state, dwAlerts, routeHelper, userProfileService, resolveTimezone) => {
          return routeHelper.selectUser()
            .then(() => resolveTimezone())
            .then(() => userProfileService.get(state.getCurrentCenterId(), $route.current.params.userId)
              .catch(err => dwAlerts.error(err, 'Failed to get user profile.')));
        },
        settings: (dwAlerts, setupService) => {
          return setupService.getSetupSettings()
            .catch(err => dwAlerts.error(err, 'Failed to get setup settings.'));
        },
        lazy: 'lazyMemberSignUp',
      }
    });

    /**
     * No Plan -> Complete
     */
    $routeProvider.when('/member-sign-up/:userId/billing/complete', {
      template: '<div><ng-include src="\'steps/04-complete.tpl.html\'"></ng-include></div>',
      controller: 'MemberSignUpNoPlanCompleteCtrl',
      controllerAs: 'vm',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['member-sign-up'],
      resolve: {
        reg: () => { return { member: true, selfRegistration: true }; },
        lazy: 'lazyMemberSignUp',
      }
    });
  })

  // .factory('filterNonEmptyCenters', () => centers => centers.filter(center => center.reservationUnitsCount > 0))
  .factory('filterHiddenCenters', () => centers => centers.filter(center => !center.hideInMemberSignUp))

  .factory('affiliatly', ($injector) => ({
    // track is incomplete
    // track: (params) => {
    //   if ($injector.has('Affiliatly')) return $injector.get('Affiliatly').track(params);
    // },

    markPurchase: (params) => {
      if ($injector.has('Affiliatly')) return $injector.get('Affiliatly').markPurchase(params);
    },

  }));
