import 'bundles/lazy-loaders.service.js';

export default angular.module('deskworks.system', [
  'ngRoute',
  'deskworks.routeHelper',
  'deskworks.dwAlerts',
  'deskworks.accessCard.service',
  'deskworks.system.service',
  'deskworks.Admin',
  'deskworks.lazyLoaders',
])

  .config(function($routeProvider) {
    $routeProvider.when('/access-cards', {
      template: '<div ng-include="\'access-card/access-card-list.tpl.html\'"></div>',
      controller: 'AccessCardListCtrl as vm',
      label: 'Access Cards',
      routeBranch: 'access-cards',
      translations: ['access-cards'],
      resolve: {
        accessCards: (dwAlerts, AccessCard, state, routeHelper) => {
          return routeHelper.auth()
            .then(() => AccessCard.query({ centerId: state.getCurrentCenterId() }).$promise
              .catch(err => dwAlerts.error(err, 'Failed to query access cards.')));
        },
        center: (state, dwAlerts, centerService) => centerService.get(state.getCurrentCenterId())
          .catch(err => dwAlerts.error(err, 'Failed to get center.')),
        lazy: 'lazyLoadSystem',
      }
    });

    $routeProvider.when('/access-cards/:accessCardId', {
      template: '<div ng-include="\'access-card/access-card-edit.tpl.html\'"></div>',
      controller: 'AccessCardEditCtrl as vm',
      label: 'Edit',
      routeBranch: 'access-cards',
      translations: ['access-cards'],
      resolve: {
        accessCard: ($route, dwAlerts, AccessCard, state, helper, routeHelper) => {
          return routeHelper.auth()
            .then(() => AccessCard.query({ centerId: state.getCurrentCenterId() }).$promise
              .catch(err => dwAlerts.error(err, 'Failed to query access cards.'))
              .then(result => helper.find(result, $route.current.params.accessCardId)));
        },
        user: (dwAlerts, state, userProfileService) => {
          return userProfileService.get(state.getCurrentCenterId(), state.getCurrentUserId())
            .catch(err => dwAlerts.error(err, 'Failed to get user profile.'));
        },
        lazy: 'lazyLoadSystem',
      }
    });

    $routeProvider.when('/administrators', {
      template: '<div ng-include="\'administrator/administrator-list.tpl.html\'"></div>',
      controller: 'AdministratorListCtrl as $ctrl',
      label: 'Administrators',
      routeBranch: 'administrators',
      translations: ['administrators'],
      resolve: {
        admins: (dwAlerts, Admin, routeHelper) => {
          return routeHelper.auth()
            .then(() => Admin.query()
              .catch(err => dwAlerts.error(err, 'Failed to query administrators.')));
        },
        lazy: 'lazyLoadSystem',
      }
    });

    $routeProvider.when('/administrators/:adminId', {
      template: '<div ng-include="\'administrator/administrator-edit.tpl.html\'"></div>',
      controller: 'AdminEditCtrl as $ctrl',
      label: 'Edit',
      routeBranch: 'administrators',
      translations: ['administrators', 'user-profile'],
      resolve: {
        admin: ($route, dwAlerts, Admin, routeHelper) => {
          return routeHelper.auth()
            .then(() => Admin.get($route.current.params.adminId)
              .catch(err => dwAlerts.error(err, 'Failed to get administrator.')));
        },
        userProfile: ($route, state, dwAlerts, userProfileService, routeHelper) => {
          return routeHelper.auth()
            .then(() => userProfileService.get(state.getCurrentCenterId(), $route.current.params.adminId)
              .catch(err => dwAlerts.error(err, 'Failed to get administrator user profile.')));
        },
        lazy: 'lazyLoadSystem',
      }
    });

    $routeProvider.when('/lead-sources', {
      template: '<div ng-include="\'lead-source/lead-source-list.tpl.html\'"></div>',
      controller: 'LeadSourceListCtrl as vm',
      label: 'Lead Sources',
      routeBranch: 'lead-sources',
      translations: ['lead-sources'],
      resolve: {
        leadSources: (dwAlerts, systemService, state, routeHelper) => {
          return routeHelper.auth()
            .then(() => systemService.getLeadSources()
              .catch(err => dwAlerts.error(err, 'Failed to query lead sources.')));
        },
        lazy: 'lazyLoadSystem',
      }
    });
  });
