import 'bundles/lazy-loaders.service.js';

export default angular.module('deskworks.bundleReservation', [
  'ngRoute',
  'deskworks.routeHelper',
  'deskworks.dwAlerts',
  'deskworks.setup.service',
  'deskworks.lazyLoaders',
])
  .config(function($routeProvider) {

    // Bundle Reservation Auth helper
    function brAuth($route, helper, routeHelper) {
      return routeHelper.auth(helper.interpolateRoute('/bundle-reservation/:reservationBundleId', $route.current.params));
    }

    /**
     * Sign-In
     */
    $routeProvider.when('/bundle-reservation/:reservationBundleId', {
      template: '<dw-sign-in id="signIn" show-sign-up="true"></dw-sign-in>',
      controller: 'BundleReservationSignInCtrl',
      controllerAs: 'vm',
      routeBranch: 'bundle-reservation',
      translations: ['sign-in'],
      hideSidebar: true,
      userTasksPolicy: false,
      resolve: {
        redirect: function($q, state, routeHelper, dwAlerts, userProfileService) {
          return routeHelper.auth(null).then(function() { // If user is logged in, redirect to Bundle Reservation / User Profile or Confirm Reservation
            return userProfileService.get(state.getCurrentCenterId(), state.getCurrentUserId())
              .catch(err => dwAlerts.error(err, 'Failed to load user profile.'))
              .then(function(user) {
                if (user.role === 'nonMember') {
                  state.setPath(state.getPath() + '/user-profile', true);
                } else {
                  state.setPath(state.getPath() + '/confirm', true);
                }
                return $q.reject();
              });
          }, function() {}); // If user is not logged in continue to Sign In form
        },
        lazy: 'lazyLoadBundleReservation',
      }
    });

    /**
     * User Profile
     */
    $routeProvider.when('/bundle-reservation/:reservationBundleId/user-profile', {
      template: '<div ng-include="\'templates/new-user-profile.tpl.html\'"></div>',
      controller: 'BundleReservationUserProfileCtrl',
      controllerAs: 'vm',
      label: 'Profile',
      routeBranch: 'bundle-reservation',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['user-profile'],
      resolve: {
        reg: () => { return { member: false, selfRegistration: true }; },
        userProfile: function(state, $q, dwAlerts, helper, routeHelper, userProfileService) {
          return routeHelper.auth(null)
            .then(() => {
              if (!state.getCurrentCenterId()) return $q.reject();
              return userProfileService.getForRegistration(state.getCurrentCenterId(), state.getCurrentUserId(), 'nonMember', 'signUp')
                .catch(err => dwAlerts.error(err, 'Failed to get user profile.'));
            }, () => {
              return userProfileService.new({}, 'nonMember', 'signUp')
                .catch(err => dwAlerts.error(err, 'Failed to get new member profile template.'));
            });
        },
        centers: function(dwAlerts, centerService) {
          centerService.resetCache();
          return centerService.getCenters()
            .catch(err => dwAlerts.error(err, 'Failed to get center.'));
        },
        lazy: 'lazyLoadBundleReservation',
      }
    });

    /**
     * Purchase
     */
    $routeProvider.when('/bundle-reservation/:reservationBundleId/user-profile/purchase/:purchaseId', {
      template: '<div ng-include="\'steps/bundle-reservation-purchase.tpl.html\'"></div>',
      controller: 'BundleReservationPurchaseCtrl',
      controllerAs: '$ctrl',
      label: 'Purchase',
      routeBranch: 'bundle-reservation',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['purchase', 'simple-reservation'],
      resolve: {
        reg: function() { return { member: false, selfRegistration: true }; },
        center: function(dwAlerts, state, centerService) {
          return centerService.get(state.getCurrentCenterId())
            .catch(err => dwAlerts.error(err, 'Failed to get center data.'));
        },
        purchase: function($route, dwAlerts, state, helper, routeHelper, purchaseService) {
          return brAuth($route, helper, routeHelper)
            .then(() => purchaseService.get(state.getCurrentCenterId(), state.getCurrentUserId(), $route.current.params.purchaseId)
              .catch(err => dwAlerts.error(err, 'Failed to get purchase.')));
        },
        lazy: 'lazyLoadBundleReservation',
      }
    });

    /**
     * Staff Help
     */
    $routeProvider.when('/bundle-reservation/:reservationBundleId/user-profile/purchase/:purchaseId/staff-help', {
      template: '<div ng-include="\'staff-help/staff-help.tpl.html\'"></div>',
      controller: 'BundleReservationStaffHelpCtrl',
      controllerAs: 'vm',
      label: 'Staff Help',
      routeBranch: 'bundle-reservation',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['simple-reservation'],
      resolve: {
        auth: brAuth,
        lazy: 'lazyLoadBundleReservation',
      }
    });

    /**
     * Billing
     */
    $routeProvider.when('/bundle-reservation/:reservationBundleId/user-profile/purchase/:purchaseId/billing', {
      template: '<div ng-include="\'templates/new-billing-profile.tpl.html\'"></div>',
      controller: 'BundleReservationBillingCtrl',
      controllerAs: 'vm',
      label: 'Billing',
      routeBranch: 'bundle-reservation',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['billing-profile', 'user-profile', 'simple-reservation'],
      resolve: {
        reg: function() { return { member: false, selfRegistration: true }; },
        userProfile: function(dwAlerts, $route, helper, state, routeHelper, userProfileService) {
          return brAuth($route, helper, routeHelper)
            .then(() => userProfileService.get(state.getCurrentCenterId(), state.getCurrentUserId())
              .catch(err => dwAlerts.error(err, 'Failed to get user profile.')));
        },
        settings: function(dwAlerts, setupService) {
          return setupService.getSetupSettings()
            .catch(err => dwAlerts.error(err, 'Failed to get setup settings.'));
        },
        center: function(dwAlerts, state, centerService) {
          return centerService.get(state.getCurrentCenterId())
            .catch(err => dwAlerts.error(err, 'Failed to get center data.'));
        },
        lazy: 'lazyLoadBundleReservation',
      }
    });

    /**
     * Complete
     */
    $routeProvider.when('/bundle-reservation/:reservationBundleId/user-profile/purchase/:purchaseId/billing/complete', {
      template: '<div ng-include="\'steps/bundle-reservation-complete.tpl.html\'"></div>',
      controller: 'BundleReservationCompleteCtrl',
      label: 'Complete',
      routeBranch: 'bundle-reservation',
      controllerAs: 'vm',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['purchase', 'simple-reservation'],
      resolve: {
        reg: function() { return { member: false, selfRegistration: true }; },
        purchase: function(dwAlerts, $route, state, helper, routeHelper, purchaseService) {
          return brAuth($route, helper, routeHelper)
            .then(() => purchaseService.get(state.getCurrentCenterId(), state.getCurrentUserId(), $route.current.params.purchaseId)
              .catch(err => dwAlerts.error(err, 'Failed to get purchase.')));
        },
        center: function(dwAlerts, $route, state, centerService) {
          return centerService.get(state.getCurrentCenterId())
            .catch(err => dwAlerts.error(err, 'Failed to get center.'));
        },
        lazy: 'lazyLoadBundleReservation',
      }
    });

    /**
     * Confirm Reservation for Member only
     */
    $routeProvider.when('/bundle-reservation/:reservationBundleId/confirm', {
      template: '<div ng-include="\'steps/bundle-reservation-confirm.tpl.html\'"></div>',
      controller: 'BundleReservationConfirmCtrl',
      controllerAs: 'vm',
      routeBranch: 'bundle-reservation',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['reservations'],
      resolve: {
        auth: brAuth,
        lazy: 'lazyLoadBundleReservation',
      }
    });

    /**
     * Complete for Member only
     */
    $routeProvider.when('/bundle-reservation/:reservationBundleId/complete', {
      template: '<div ng-include="\'steps/bundle-reservation-complete-member.tpl.html\'"></div>',
      controller: 'BundleReservationCompleteCtrl',
      label: 'Complete',
      routeBranch: 'bundle-reservation',
      controllerAs: 'vm',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['simple-reservation'],
      resolve: {
        purchase: function() { return null; },
        center: function() { return null; },
        lazy: 'lazyLoadBundleReservation',
      }
    });
  });
