import 'bundles/lazy-loaders.service.js';
import ReservationUnit from 'shared/services/inventory/reservation-unit.service';

export default angular.module('deskworks.inventory', [
  'ngRoute',
  'deskworks.routeHelper',
  'deskworks.dwAlerts',
  'deskworks.center.service',
  'deskworks.inventory.membershipCategory.service',
  'deskworks.reservationCategory.service',
  'deskworks.inventory.priceList.service',
  'deskworks.inventory.product.service',
  'deskworks.ProductCategory',
  'deskworks.lazyLoaders',
  ReservationUnit.name,
])

  .config(function($routeProvider) {
    /**
     * Membership Categories
     */
    $routeProvider.when('/membership-types', {
      template: '<div ng-include="\'membership-category/membership-category-list.tpl.html\'"></div>',
      controller: 'MembershipCategoryListCtrl as vm',
      label: 'Membership Categories',
      routeBranch: 'membership-types',
      translations: ['membership-categories'],
      resolve: {
        membershipCategories: function(dwAlerts, membershipCategoryService, routeHelper) {
          return routeHelper.auth().then(function() {
            return membershipCategoryService.query()
              .catch(function(err) { return dwAlerts.error(err, 'Failed to query membership categories.'); });
          });
        },
        lazy: 'lazyLoadInventory'
      }
    });

    $routeProvider.when('/membership-types/:membershipCategoryId', {
      template: '<div ng-include="\'membership-category/membership-category-edit.tpl.html\'"></div>',
      controller: 'MembershipCategoryEditCtrl as vm',
      label: 'Edit',
      routeBranch: 'membership-types',
      translations: ['membership-categories'],
      resolve: {
        membershipCategories: function(dwAlerts, membershipCategoryService, routeHelper) {
          return routeHelper.auth().then(function() {
            return membershipCategoryService.query()
              .catch(function(err) { return dwAlerts.error(err, 'Failed to query membership categories.'); });
          });
        },
        lazy: 'lazyLoadInventory'
      }
    });

    /**
     * Price Lists
     */
    $routeProvider.when('/pricelists', {
      template: '<div ng-include="\'price-list/price-list-list.tpl.html\'"></div>',
      controller: 'PriceListListCtrl as vm',
      label: 'Price Lists',
      routeBranch: 'pricelists',
      reloadOnSearch: false,
      translations: ['price-lists', 'reports'],
      resolve: {
        priceLists: function(dwAlerts, priceListService, state, routeHelper) {
          return routeHelper.auth().then(function() {
            return priceListService.getPricelists(state.getCurrentCenterId())
              .catch(function(err) { return dwAlerts.error(err, 'Failed to query price lists.'); });
          });
        },
        centers: function(dwAlerts, centerService) {
          return centerService.getCenters()
            .catch(function(err) { return dwAlerts.error(err, 'Failed to query centers.'); });
        },
        lazy: 'lazyLoadInventory'
      }
    });

    $routeProvider.when('/pricelists/:priceListId', {
      template: '<div ng-include="\'price-list/price-list-edit.tpl.html\'"></div>',
      controller: 'PriceListEditCtrl as vm',
      label: 'Edit',
      routeBranch: 'pricelists',
      translations: ['price-lists', 'products'],
      resolve: {
        priceList: function($route, dwAlerts, priceListService, state, routeHelper) {
          return routeHelper.auth().then(function() {
            return priceListService.getPricelist(state.getCurrentCenterId(), $route.current.params.priceListId)
              .catch(function(err) { return dwAlerts.error(err, 'Failed to get price list.'); });
          });
        },
        lazy: 'lazyLoadInventory'
      }
    });

    /**
     * Products
     */
    $routeProvider.when('/products', {
      template: '<div ng-include="\'product/product-list.tpl.html\'"></div>',
      controller: 'ProductListCtrl as vm',
      label: 'Products',
      routeBranch: 'products',
      reloadOnSearch: false,
      translations: ['products', 'recurring-billing', 'reports'],
      resolve: {
        products: (dwAlerts, productService, routeHelper) =>
          routeHelper.auth()
            .then(() => productService.getProducts()
              .catch(err => dwAlerts.error(err, 'Failed to query products.'))),
        membershipCategories: (dwAlerts, membershipCategoryService, routeHelper) =>
          routeHelper.auth()
            .then(() => membershipCategoryService.query()
              .catch(err => dwAlerts.error(err, 'Failed to query membership categories.'))),
        reservationCategories: (dwAlerts, reservationCategoryService, state, routeHelper) =>
          routeHelper.auth()
            .then(() => reservationCategoryService.query(state.getCurrentCenterId())
              .catch(err => dwAlerts.error(err, 'Failed to query reservation categories.'))),
        lazy: 'lazyLoadInventory',
      },
    });

    $routeProvider.when('/products/:productId', {
      template: '<div ng-include="\'product/product-edit.tpl.html\'"></div>',
      controller: 'ProductEditCtrl as vm',
      label: 'Edit',
      routeBranch: 'products',
      translations: ['products', 'recurring-billing', 'printer-usage-settings'],
      resolve: {
        product: function($route, dwAlerts, productService, routeHelper) {
          return routeHelper.auth().then(function() {
            return productService.getProduct($route.current.params.productId)
              .catch(function(err) { return dwAlerts.error(err, 'Failed to get product.'); });
          });
        },
        settings: function(dwAlerts, setupService) {
          return setupService.getSetupSettings()
            .catch(function(err) { return dwAlerts.error(err, 'Failed to get setup settings.'); });
        },
        productCategories: function(dwAlerts, ProductCategory, state, routeHelper) {
          return routeHelper.auth().then(function() {
            return ProductCategory.query(state.getCurrentCenterId())
              .catch(function(err) { return dwAlerts.error(err, 'Failed to query product categories.'); });
          });
        },
        lazy1: 'lazyLoadInventory',
        lazy2: 'lazyLoadQuickBooks',
        lazy3: 'lazyLoadDeskworksSweep',
      }
    });

    /**
     * Reservation Categories
     */
    $routeProvider.when('/reservation-types', {
      template: '<div ng-include="\'reservation-category/reservation-category-list.tpl.html\'"></div>',
      controller: 'InventoryReservationCategoriesCtrl as vm',
      label: 'Reservation Categories',
      routeBranch: 'reservation-types',
      translations: ['reservation-categories'],
      resolve: {
        reservationCategories: function(dwAlerts, reservationCategoryService, state, routeHelper) {
          return routeHelper.auth().then(function() {
            return reservationCategoryService.query(state.getCurrentCenterId())
              .catch(function(err) { return dwAlerts.error(err, 'Failed to query reservation categories.'); });
          });
        },
        lazy: 'lazyLoadInventory'
      }
    });

    $routeProvider.when('/reservation-types/:reservationTypeId', {
      template: '<div ng-include="\'reservation-category/reservation-category-edit.tpl.html\'"></div>',
      controller: 'InventoryReservationCategoryEditCtrl as vm',
      label: 'Edit',
      routeBranch: 'reservation-types',
      translations: ['reservation-categories'],
      resolve: {
        reservationCategory: function($route, dwAlerts, reservationCategoryService, state, routeHelper) {
          return routeHelper.auth().then(function() {
            return reservationCategoryService.get(state.getCurrentCenterId(), $route.current.params.reservationTypeId)
              .catch(function(err) { return dwAlerts.error(err, 'Failed to get reservation category.'); });
          });
        },
        reservationCategories: function(dwAlerts, reservationCategoryService, state, routeHelper) {
          return routeHelper.auth().then(function() {
            return reservationCategoryService.query(state.getCurrentCenterId())
              .catch(function(err) { return dwAlerts.error(err, 'Failed to query reservation categories.'); });
          });
        },
        lazy: 'lazyLoadInventory'
      }
    });

    /**
     * Reservation Units
     */
    $routeProvider.when('/reservation-units', {
      template: '<div ng-include="\'reservation-unit/reservation-unit-list.tpl.html\'"></div>',
      controller: 'InventoryReservationUnitListCtrl as vm',
      label: 'Reservation Units',
      routeBranch: 'reservation-units',
      translations: ['reservation-units', 'reservation-rules'],
      resolve: {
        reservationUnits: function(dwAlerts, ReservationUnit, state, routeHelper) {
          return routeHelper.auth().then(function() {
            return ReservationUnit.query(state.getCurrentCenterId())
              .catch(function(err) { return dwAlerts.error(err, 'Failed to query reservation units.'); });
          });
        },
        lazy: 'lazyLoadInventory'
      }
    });

    $routeProvider.when('/reservation-units/:reservationUnitId', {
      template: '<div ng-include="\'reservation-unit/reservation-unit-edit.tpl.html\'"></div>',
      controller: 'InventoryReservationUnitEditCtrl as $ctrl',
      label: 'Edit',
      routeBranch: 'reservation-units',
      translations: ['reservation-units', 'reservation-rules'],
      resolve: {
        unit: function($route, dwAlerts, ReservationUnit, state, routeHelper) {
          return routeHelper.auth()
            .then(() => ReservationUnit.get(state.getCurrentCenterId(), $route.current.params.reservationUnitId)
              .catch(err => dwAlerts.error(err, 'Failed to get reservation unit.')));
        },
        categories: function(dwAlerts, reservationCategoryService, state) {
          return reservationCategoryService.query(state.getCurrentCenterId())
            .catch(err => dwAlerts.error(err, 'Failed to query reservation categories.'));
        },
        lazy: 'lazyLoadInventory'
      }
    });

    /**
     * Product Categories
     */
    $routeProvider.when('/product-categories', {
      template: '<div ng-include="\'product-categories/product-categories.tpl.html\'"></div>',
      controller: 'ProductCategoriesCtrl as $ctrl',
      label: 'Product Categories',
      routeBranch: 'product-categories',
      translations: ['product-categories'],
      resolve: {
        categories: function(dwAlerts, ProductCategory, state, routeHelper) {
          return routeHelper.auth().then(function() {
            return ProductCategory.query()
              .catch(function(err) { return dwAlerts.error(err, 'Failed to query product categories.'); });
          });
        },
        lazy: 'lazyLoadInventory'
      }
    });

    /**
     * Discount Codes
     */
    $routeProvider.when('/discount-codes', {
      template: '<div ng-include="\'discount-codes/discount-codes.tpl.html\'"></div>',
      controller: 'DiscountCodesCtrl as $ctrl',
      routeBranch: 'discount-codes',
      translations: ['discount-codes'],
      resolve: {
        auth: (routeHelper) => routeHelper.auth(),
        lazy: 'lazyLoadInventory'
      }
    });
  });
