import lazyLoaders from 'bundles/lazy-loaders.service';

export default angular.module('deskworks.ezeep', [
  'ngRoute',
  'deskworks.routeHelper',
  lazyLoaders.name,
])

  .config(function($routeProvider) {
    $routeProvider.when('/ezeep-errors', {
      template: '<div ng-include="\'errors/ezeep-errors.tpl.html\'"></div>',
      controller: 'EzeepErrorsCtrl as $ctrl',
      routeBranch: 'ezeep-errors',
      translations: ['ezeep-errors'],
      resolve: {
        auth: routeHelper => routeHelper.auth(),
        lazy: 'lazyLoadEzeep',
      }
    });
  });
