import 'bundles/lazy-loaders.service.js';

export default angular.module('deskworks.dashboard', [
  'ngRoute',
  'deskworks.routeHelper',
  'deskworks.state',
  'deskworks.dwAlerts.service',
  'deskworks.Admin',
  'deskworks.userProfile.service',
  'deskworks.setup.service',
  'deskworks.lazyLoaders',
])

  .config(function($routeProvider) {
    $routeProvider.when('/dashboard', {
      template: '<div ng-include="\'dashboard-user/dashboard-user.tpl.html\'"></div>',
      controller: 'DashboardUserCtrl',
      controllerAs: 'vm',
      label: 'Dashboard',
      routeBranch: 'dashboard',
      translations: ['dashboard'],
      resolve: {
        user: function($q, dwAlerts, routeHelper, state, userProfileService, resolveTimezone) {
          return routeHelper.auth()
            .then(function() {

            if(state.isLoggedAdmin()) { // redirect to Admin Dashboard for admins/coordinators
              state.setPath('/dashboard-admin', true);
              return $q.reject();
            }

            return resolveTimezone()
              .then(() => userProfileService.get(state.getCurrentCenterId(), state.getCurrentUserId())
                .catch(err => dwAlerts.error(err, 'Failed to get user profile.')));
          });
        },
        userLogged: function(dwAlerts, state, routeHelper, userProfileService, resolveTimezone) {
          return routeHelper.auth()
            .then(() => resolveTimezone())
            .then(function() {
            return userProfileService.get(state.getCurrentCenterId(), state.getLoggedUserId())
              .catch(err => dwAlerts.error(err, 'Failed to get user profile.'));
          });
        },
        settings: function(dwAlerts, setupService) {
          return setupService.getSetupSettings()
            .catch(err => dwAlerts.error(err, 'Failed to get setup settings.'));
        },
        lazy: 'lazyLoadDashboard',
      }
    });
  })

  .config(function($routeProvider) {
    $routeProvider.when('/dashboard-admin', {
      template: '<div ng-include="\'dashboard-admin/dashboard-admin.tpl.html\'"></div>',
      controller: 'DashboardAdminCtrl as vm',
      label: 'Dashboard',
      routeBranch: 'dashboard',
      translations: ['dashboard'],
      resolve: {
        admin: function($q, dwAlerts, routeHelper, state, userProfileService, Admin, resolveTimezone) {
          return routeHelper.auth()
            .then(() => resolveTimezone())
            .then(() => $q.all([
              userProfileService.get(state.getCurrentCenterId(), state.getLoggedUserId())
                .catch(err => dwAlerts.error(err, 'Failed to get admin profile.')),
              Admin.get(state.getLoggedUserId())
                .catch(err => dwAlerts.error(err, 'Failed to get admin.'))
            ])
              .then(results => angular.extend(results[1], results[2])));
        },
        settings: function(dwAlerts, setupService) {
          return setupService.getSetupSettings()
            .catch(err => dwAlerts.error(err, 'Failed to get setup settings.'));
        },
        lazy: 'lazyLoadDashboard',
      }
    })
  })

  .config($routeProvider => {
    $routeProvider.when('/access-code-of-the-day', {
      template: '<div ng-include="\'access-code-of-the-day/access-code-of-the-day.tpl.html\'"></div>',
      controller: 'AccessCodeOfTheDayCtrl as $ctrl',
      routeBranch: 'access-code-of-the-day',
      // translations: ['dashboard'],
      userTasksPolicy: false,
      hideSidebar: true,
      resolve: {
        lazy: 'lazyLoadDashboard',
      }
    });
  });
