import 'bundles/lazy-loaders.service.js';
import ReservationField from 'shared/services/setup/reservation-field.service';

export default angular.module('deskworks.reservationTablet', [
  'ngStorage',
  'ngRoute',
  'deskworks.routeHelper',
  'deskworks.dwAlerts',
  'deskworks.calendarService',
  'deskworks.center.service',
  'deskworks.userProfile.service',
  'deskworks.lazyLoaders',
  ReservationField.name,
])

  .config(function($routeProvider) {
    $routeProvider.when('/reservation-tablet-setup', {
      template: '<div ng-include="\'reservation-tablet-setup/setup.tpl.html\'"></div>',
      controller: 'ReservationTabletSetupCtrl as $ctrl',
      routeBranch: 'reservation-tablet-setup',
      translations: ['reservation-tablet'],
      resolve: {
        categories: (dwAlerts, state, routeHelper, calendarService) => routeHelper.auth()
          .then(() => calendarService.query(state.getCurrentCenterId())
            .catch(function(err) { return dwAlerts.error(err, 'Failed to get calendar data.'); })),
        lazy: 'lazyReservationTablet',
      }
    });

    const signInUrl = '/reservation-tablet-wizard/sign-in';

    /**
     * Wizard: 01 - Schedule
     */
    $routeProvider.when('/reservation-tablet-wizard', {
      template: '<div ng-include="\'reservation-tablet-wizard/01-schedule.tpl.html\'"></div>',
      controller: 'ReservationTabletScheduleCtrl as $ctrl',
      routeBranch: 'reservation-tablet-wizard',
      hideSidebar: true,
      userTasksPolicy: false,
      translations: ['reservation-tablet'],
      resolve: {
        lazy: 'lazyReservationTablet',
        validate: validateReservationTablet => validateReservationTablet(),
      }
    });

    /**
     * Wizard: 02 - Sign In
     */
    $routeProvider.when('/reservation-tablet-wizard/sign-in', {
      template: '<div ng-include="\'reservation-tablet-wizard/02-sign-in.tpl.html\'"></div>',
      // templateUrl: 'sign-in/sign-in.tpl.html',
      controller: 'ReservationTabletSignInCtrl as $ctrl',
      routeBranch: 'reservation-tablet-wizard',
      hideSidebar: true,
      userTasksPolicy: false,
      reloadOnSearch: false,
      translations: ['sign-in'],
      resolve: {
        lazy: 'lazyReservationTablet',
        validate: validateReservationTablet => validateReservationTablet(),
      },
    });

    /**
     * Wizard: 03 - Reserve
     */
    $routeProvider.when('/reservation-tablet-wizard/reserve', {
      template: '<div ng-include="\'reservation-tablet-wizard/03-reserve.tpl.html\'"></div>',
      controller: 'ReservationTabletReserveCtrl as $ctrl',
      routeBranch: 'reservation-tablet-wizard',
      hideSidebar: true,
      reloadOnSearch: false,
      userTasksPolicy: false,
      translations: ['reservations', /*'reservation-tablet'*/],
      resolve: {
        user: function($q, userProfileService, state, dwAlerts, routeHelper, resolveTimezone) {
          return $q.all([routeHelper.auth(signInUrl), resolveTimezone()])
            .then(() => userProfileService.get(state.getCurrentCenterId(), state.getCurrentUserId())
              .catch(err => dwAlerts.error(err, 'Failed to get user profile.')));
        },
        center: function(state, dwAlerts, centerService, routeHelper) {
          return routeHelper.auth(signInUrl)
            .then(() => centerService.get(state.getCurrentCenterId())
              .catch(err => dwAlerts.error(err, 'Failed to get center.')));
        },
        calData: function($q, $filter, state, helper, dwAlerts, calendarService, routeHelper, resolveTimezone) {
          return $q.all([routeHelper.auth(signInUrl), resolveTimezone()])
            .then(() => {
              let date = $filter('toDate')(state.getParam('date'), 'Z');
              date = moment.isMoment(date) ? date : moment();
              return calendarService.query(state.getCurrentCenterId(), date, date, state.getParam('role'))
                .catch(err => dwAlerts.error(err, 'Failed to fetch calendar data.'));
            });
        },
        reservationFields: (dwAlerts, ReservationField, session) => session.wait().catch(angular.noop)
          .then(() => ReservationField.query()
            .catch(err => dwAlerts.error(err, 'Failed to query reservation fields.'))),
        lazy: 'lazyReservationTablet',
        validate: validateReservationTablet => validateReservationTablet(),
      }
    });

    /**
     * Wizard: 04 - Complete
     */
    $routeProvider.when('/reservation-tablet-wizard/complete', {
      template: '<div ng-include="\'reservation-tablet-wizard/04-complete.tpl.html\'"></div>',
      controller: 'ReservationTabletCompleteCtrl as $ctrl',
      routeBranch: 'reservation-tablet-wizard',
      hideSidebar: true,
      reloadOnSearch: false,
      userTasksPolicy: false,
      translations: ['reservation-tablet'],
      resolve: {
        lazy: 'lazyReservationTablet',
        validate: validateReservationTablet => validateReservationTablet(),
      }
    });
  })

  .factory('validateReservationTablet', ($localStorage, $route, dwAlerts, state) => {
    return function() {
      $localStorage.reservationTablet = $localStorage.reservationTablet || {};
      const ls = $localStorage.reservationTablet;
      const prefix = 'Missing $localStorage.reservationTablet';

      if (!ls.centerId) return dwAlerts.error(`${prefix}.centerId`);
      if (!ls.login) return dwAlerts.error(`${prefix}.login`);
      if (!ls.password) return dwAlerts.error(`${prefix}.password`);
      if (!ls.unitId) return dwAlerts.error(`${prefix}.unitId`);

      if (ls.unitId !== +state.getParam('unit')) {
        state.setParam('unit', ls.unitId);
        $route.reload();
        throw false;
      }

      return true;
    };
  });
