'use strict';

import 'angular';

/**
 * Import common JS
 */
function importJs(r) {
  r.keys().forEach(r);
}
importJs(require.context('../common/', true, /^(?!.*\.test\.js$)^(?!.*\.spec\.js$).*\.js$/));

/**
 * Import Templates
 */
const ctx = require.context('../common/', true, /.*\.html$/);
angular.module('deskworks.templates', []).run(['$templateCache', function($templateCache) {
  ctx.keys().forEach(key => $templateCache.put(key.replace(/^\.\//, ''), ctx(key)));
}]);
