import lazyLoaders from 'bundles/lazy-loaders.service';
import ReservationField from 'shared/services/setup/reservation-field.service';
import wizardReservationRoutes from 'shared/wizard-reservation/wizard-reservation-routes';

export default angular.module('deskworks.bookItNow', [
  'ngRoute',
  'session',
  'deskworks.rcHelper',
  'deskworks.center.service',
  'deskworks.calendarService',
  'deskworks.userProfile.service',
  'deskworks.purchase.service',
  'deskworks.setup.service',
  lazyLoaders.name,
  ReservationField.name,
])
  .config(function($routeProvider) {

    const authUrl = '/book-it-now/sign-in';

    /**
     * 01. Book It Now: Calendar
     */
    $routeProvider.when('/book-it-now', {
      template: '<rc-calendar cal-data="$resolve.calData" settings="$resolve.settings" next="$ctrl.next()" show-pricing="true"></rc-calendar>',
      controller: 'BookItNowCalendarCtrl as $ctrl',
      routeBranch: 'book-it-now',
      hideSidebar: true,
      hideBanner: true,
      userTasksPolicy: false,
      translations: ['reservations', 'book-it-now'],
      resolve: {
        calData: function($q, $filter, state, helper, dwAlerts, calendarService, rcHelper, resolveTimezone, session, centerService) {
          const centersPromise = centerService.getCenters().catch(err => dwAlerts.error(err, 'Failed to query centers.'));
          return $q.all([centersPromise, session.wait().catch(angular.noop), resolveTimezone()])
            .then(result => {
              let date = $filter('toDate')(state.getParam('date'), 'Z');
              date = moment.isMoment(date) ? date : moment();

              if (!result[0].length) return dwAlerts.error('No centers available');
              if (!state.getCurrentCenterId()) state.setCurrentCenterId(result[0][0].id); // auto-select center

              return calendarService.query(state.getCurrentCenterId(), rcHelper.weekStart(date), rcHelper.weekEnd(date))
                .catch(err => dwAlerts.error(err, 'Failed to fetch calendar data.'));
            });
        },
        settings: function(dwAlerts, setupService) {
          return setupService.getSetupSettings()
            .catch(err => dwAlerts.error(err, 'Failed to get setup settings.'));
        },
        lazy: 'lazyBookItNow',
      },
    });

    /**
     * Wizard Reservation routes
     */
    wizardReservationRoutes.routes.forEach(route => {
      $routeProvider.when(`/book-it-now/${route.url}`, wrap(wizardReservationRoutes[route.id]));
    });

    function wrap(route) {
      return {
        ...route,
        routeBranch: 'book-it-now',
        hideSidebar: true,
        resolve: {
          ...route.resolve,
          lazy: 'lazyBookItNow',
        },
      };
    }

  });
