import lazyLoaders from 'bundles/lazy-loaders.service.js';

export default angular.module('deskworks.checkInForm', [
  lazyLoaders.name,
])

  .config(function($routeProvider) {
    $routeProvider.when('/check-in', {
      template: '<div ng-include="\'check-in/check-in.tpl.html\'"></div>',
      controller: 'CheckInFormCtrl as vm',
      hideSidebar: true,
      userTasksPolicy: false,
      landing: true,
      resolve: {
        center: (state, helper, dwAlerts, centerService) => centerService.getCenters()
          .catch(err => dwAlerts.error(err, 'Failed to query centers.'))
          .then(centers => helper.find(centers, state.getCurrentCenterId()) || centers[0] || {}),
        lazy: 'lazyLoadCheckInForm',
      },
    });
  });
