import 'bundles/lazy-loaders.service.js';

export default angular.module('deskworks.usage', [
  'ngRoute',
  'deskworks.routeHelper',
  'deskworks.dwAlerts',
  'deskworks.usageEntryService',
  'deskworks.checkInLog.service',
  'deskworks.memberUsageService',
  'deskworks.lazyLoaders',
])

  .config(function($routeProvider) {
    $routeProvider.when('/usage-entry', {
      template: '<div ng-include="\'usage-entry/usage-entry-list.tpl.html\'"></div>',
      controller: 'UsageEntryListCtrl as $ctrl',
      routeBranch: 'usage-entry',
      translations: ['usage-entry'],
      resolve: {
        usageList: function(dwAlerts, usageEntryService, state, routeHelper) {
          return routeHelper.auth()
            .then(() => routeHelper.selectUser())
            .then(() => usageEntryService.query(state.getCurrentCenterId(), state.getCurrentUserId(), 1, 15)
              .catch(err => dwAlerts.error(err, 'Failed to query usage entry.')));
        },
        user: function(dwAlerts, userProfileService, state, routeHelper, resolveTimezone) {
          return routeHelper.auth()
            .then(() => routeHelper.selectUser())
            .then(() => resolveTimezone())
            .then(() => userProfileService.get(state.getCurrentCenterId(), state.getCurrentUserId())
              .catch(err => dwAlerts.error(err, 'Failed to get user profile.')));
        },
        lazy: 'lazyLoadUsage',
      }
    });

    $routeProvider.when('/usage-entry/:usageId', {
      template: '<div ng-include="\'usage-entry/usage-entry-edit.tpl.html\'"></div>',
      controller: 'UsageEntryEditCtrl as $ctrl',
      routeBranch: 'usage-entry',
      translations: ['usage-entry'],
      resolve: {
        usage: function($route, dwAlerts, usageEntryService, state, routeHelper) {
          return routeHelper.auth()
            .then(() => routeHelper.selectUser())
            .then(() => usageEntryService.get(state.getCurrentCenterId(), state.getCurrentUserId(), $route.current.params.usageId)
              .catch(err => dwAlerts.error(err, 'Failed to get usage entry.')));
        },
        user: function(dwAlerts, userProfileService, state, routeHelper, resolveTimezone) {
          return routeHelper.auth()
            .then(() => routeHelper.selectUser())
            .then(() => resolveTimezone())
            .then(() => userProfileService.get(state.getCurrentCenterId(), state.getCurrentUserId())
              .catch(err => dwAlerts.error(err, 'Failed to get user profile.')));
        },
        lazy: 'lazyLoadUsage',
      }
    });

    /**
     * OLD Member Usage
     */
    $routeProvider.when('/member-usage', {
      template: '<div ng-include="\'view-usage/view-usage.tpl.html\'"></div>',
      controller: 'ViewUsageCtrl as $ctrl',
      routeBranch: 'member-usage',
      translations: ['view-usage'],
      resolve: {
        memberUsageList: function(dwAlerts, memberUsageService, state, routeHelper) {
          return routeHelper.auth()
            .then(() => routeHelper.selectUser())
            .then(() => memberUsageService.query(state.getCurrentCenterId(), state.getCurrentUserId())
              .catch(err => dwAlerts.error(err, 'Failed to query member usages.')));
        },
        user: function(dwAlerts, userProfileService, state, routeHelper, resolveTimezone) {
          return routeHelper.auth()
            .then(() => routeHelper.selectUser())
            .then(() => resolveTimezone())
            .then(() => userProfileService.get(state.getCurrentCenterId(), state.getCurrentUserId())
              .catch(err => dwAlerts.error(err, 'Failed to get user profile.')));
        },
        lazy: 'lazyLoadUsage',
      }
    });

    /**
     * New Usage Tracking migrated from canopy branch
     */
    $routeProvider.when('/usage-tracking', {
      template: '<div ng-include="\'usage-tracking/usage-tracking.tpl.html\'"></div>',
      controller: 'UsageTrackingCtrl as $ctrl',
      routeBranch: 'usage-tracking',
      translations: ['usage-tracking'],
      resolve: {
        user: function(dwAlerts, userProfileService, state, routeHelper, resolveTimezone) {
          return routeHelper.auth()
            .then(() => routeHelper.selectUser())
            .then(() => resolveTimezone())
            .then(() => userProfileService.get(state.getCurrentCenterId(), state.getCurrentUserId())
              .catch(err => dwAlerts.error(err, 'Failed to get user profile.')));
        },
        lazy: 'lazyLoadUsage',
      }
    });

    $routeProvider.when('/member-log', {
      template: '<div ng-include="\'check-in-log/check-in-log.tpl.html\'"></div>',
      controller: 'CheckInLogCtrl as $ctrl',
      routeBranch: 'member-log',
      translations: ['check-in-log'],
      resolve: {
        user: function(dwAlerts, userProfileService, state, routeHelper, resolveTimezone) {
          return routeHelper.auth()
            .then(() => routeHelper.selectUser())
            .then(() => resolveTimezone())
            .then(() => userProfileService.get(state.getCurrentCenterId(), state.getCurrentUserId())
              .catch(err => dwAlerts.error(err, 'Failed to get user profile.')));
        },
        lazy: 'lazyLoadUsage',
      }
    });

    $routeProvider.when('/manual-check-in-log', {
      template: '<div ng-include="\'manual-check-in-log/manual-check-in-log.tpl.html\'"></div>',
      controller: 'ManualCheckInLogCtrl as $ctrl',
      routeBranch: 'manual-check-in-log',
      translations: ['check-in-log'],
      resolve: {
        user: function(dwAlerts, userProfileService, state, routeHelper, resolveTimezone) {
          return routeHelper.auth()
            .then(() => routeHelper.selectUser())
            .then(() => resolveTimezone())
            .then(() => userProfileService.get(state.getCurrentCenterId(), state.getCurrentUserId())
              .catch(err => dwAlerts.error(err, 'Failed to get user profile.')));
        },
        lazy: 'lazyLoadUsage',
      }
    });
  });
