import 'bundles/lazy-loaders.service.js';
import UserField from 'shared/services/setup/user-field.service';

export default angular.module('deskworks.setup', [
  'ngRoute',
  'deskworks.routeHelper',
  'deskworks.dwAlerts',
  'deskworks.setup.service',
  'deskworks.bank.service',
  'deskworks.center.service',
  'deskworks.allowedHost.service',
  'deskworks.Task',
  'deskworks.Admin',
  'deskworks.Owner',
  'deskworks.Region',
  UserField.name,
  'deskworks.Roles',
  'deskworks.lazyLoaders',
])

  .config(function($routeProvider) {
    $routeProvider.when('/setup/settings', {
      template: '<div ng-include="\'settings/settings.tpl.html\'"></div>',
      controller: 'SetupSettingsCtrl as vm',
      label: 'Setup Settings',
      routeBranch: 'setup/settings',
      translations: ['setup-settings'],
      resolve: {
        settings: function(dwAlerts, setupService, state, routeHelper) {
          return routeHelper.auth().then(function() {
            return setupService.getSetupSettings()
              .catch(function(err) { return dwAlerts.error(err, 'Failed to get setup settings.'); });
          });
        },
        locales: function(dwAlerts, setupService) {
          return setupService.queryLocales()
            .catch(function(err) { return dwAlerts.error(err, 'Failed to query locales.'); });
        },
        lazy1: 'lazyLoadSetup',
        lazy2: 'lazyLoadAffiliatly',
      }
    });

    $routeProvider.when('/setup/bar-upload-settings', {
      template: '<div ng-include="\'bar-upload-settings/bar-upload-settings.tpl.html\'"></div>',
      controller: 'BarUploadSettingsCtrl as vm',
      label: 'Bar Upload Settings',
      routeBranch: 'setup/bar-upload-settings',
      translations: ['bar-upload-settings'],
      resolve: {
        settings: function(dwAlerts, setupService, state, routeHelper) {
          return routeHelper.auth().then(function() {
            return setupService.getBarUploadSettings(state.getCurrentCenterId())
              .catch(function(err) { return dwAlerts.error(err, 'Failed to get bar upload settings.'); });
          });
        },
        lazy: 'lazyLoadSetup'
      }
    });

    $routeProvider.when('/banks', {
      template: '<div ng-include="\'banks/banks.tpl.html\'"></div>',
      controller: 'SetupBanksCtrl as $ctrl',
      label: 'Banks',
      routeBranch: 'banks',
      translations: ['banks'],
      resolve: {
        banks: (dwAlerts, bankService, state, routeHelper) => {
          return routeHelper.auth()
            .then(() => bankService.query()
              .catch(err => dwAlerts.error(err, 'Failed to query banks.')));
        },
        lazy: 'lazyLoadSetup',
      }
    });

    $routeProvider.when('/banks/:bankId', {
      template: '<div ng-include="\'banks/bank-edit.tpl.html\'"></div>',
      controller: 'SetupBankEditCtrl as $ctrl',
      label: 'Bank Edit',
      routeBranch: 'banks',
      translations: ['banks'],
      resolve: {
        bank: ($route, dwAlerts, bankService, state, routeHelper) => {
          return routeHelper.auth()
            .then(() => bankService.get($route.current.params.bankId)
                .catch(err => dwAlerts.error(err, 'Failed to get bank.')));
        },
        lazy: 'lazyLoadSetup',
      }
    });

    $routeProvider.when('/centers', {
      template: '<div ng-include="\'center/center-list.tpl.html\'"></div>',
      controller: 'CenterListCtrl as vm',
      label: 'Centers',
      routeBranch: 'centers',
      translations: ['centers'],
      resolve: {
        centers: function(dwAlerts, centerService, routeHelper) {
          return routeHelper.auth()
            .then(() => centerService.getCenters()
              .catch(err => dwAlerts.error(err, 'Failed to query centers.')));
        },
        admin: function(dwAlerts, Admin, routeHelper, state) {
          return routeHelper.auth()
            .then(() => {
              if (!state.isLoggedAdmin()) return;
              return Admin.get(state.getLoggedUserId())
                .catch(err => dwAlerts.error(err, 'Failed to get admin.'));
            });
        },
        lazy: 'lazyLoadSetup',
      }
    });

    $routeProvider.when('/centers/:centerId', {
      template: '<div ng-include="\'center/center-edit.tpl.html\'"></div>',
      controller: 'CenterEditCtrl as vm',
      label: 'Edit',
      routeBranch: 'centers',
      translations: ['centers', 'reservation-rules'],
      resolve: {
        center: function($route, dwAlerts, centerService, state, routeHelper) {
          return routeHelper.auth()
            .then(() => centerService.get($route.current.params.centerId)
              .catch(err => dwAlerts.error(err, 'Failed to get center.')));
        },
        settings: function(dwAlerts, setupService) {
          return setupService.getSetupSettings()
            .catch(err => dwAlerts.error(err, 'Failed to get setup settings.'));
        },
        lazy: 'lazyLoadSetup',
      }
    });

    $routeProvider.when('/system-emails', {
      template: '<div ng-include="\'system-email/system-email-list.tpl.html\'"></div>',
      controller: 'SystemEmailListCtrl as $ctrl',
      label: 'System Emails',
      routeBranch: 'system-emails',
      translations: ['system-emails'],
      resolve: {
        systemEmails: (dwAlerts, systemService, routeHelper) => {
          return routeHelper.auth()
            .then(() => systemService.getSystemEmails()
              .catch(err => dwAlerts.error(err, 'Failed to query system emails.')));
        },
        lazy: 'lazyLoadSetup',
      }
    });

    $routeProvider.when('/system-emails/:systemEmailId', {
      template: '<div ng-include="\'system-email/system-email-edit.tpl.html\'"></div>',
      controller: 'SystemEmailEditCtrl as $ctrl',
      label: 'Edit',
      routeBranch: 'system-emails',
      translations: ['system-emails'],
      resolve: {
        systemEmail: ($route, dwAlerts, systemService, state, routeHelper) => {
          return routeHelper.auth()
            .then(() => systemService.getSystemEmail($route.current.params.systemEmailId)
              .catch(err => dwAlerts.error(err, 'Failed to get system email.')));
        },
        lazy: 'lazyLoadSetup',
      }
    });

    $routeProvider.when('/tasks', {
      template: '<div ng-include="\'task/task-list.tpl.html\'"></div>',
      controller: 'TaskListCtrl as $ctrl',
      label: 'Tasks',
      routeBranch: 'tasks',
      translations: ['tasks'],
      resolve: {
        tasks: function(dwAlerts, Task, state, routeHelper) {
          return routeHelper.auth()
            .then(() => Task.query()
              .catch(err => dwAlerts.error(err, 'Failed to query tasks.')));
        },
        roles: (dwAlerts, Roles) => Roles.query()
          .catch(err => dwAlerts.error(err, 'Failed to query user roles.')),
        lazy: 'lazyLoadSetup',
      }
    });

    $routeProvider.when('/tasks/:taskId', {
      template: '<div ng-include="\'task/task-edit.tpl.html\'"></div>',
      controller: 'TaskEditCtrl as $ctrl',
      label: 'Edit',
      routeBranch: 'tasks',
      translations: ['tasks'],
      resolve: {
        task: function($route, dwAlerts, Task, state, routeHelper) {
          return routeHelper.auth()
            .then(() => Task.get($route.current.params.taskId)
              .catch(err => dwAlerts.error(err, 'Failed to get task.')));
        },
        roles: (dwAlerts, Roles) => Roles.query()
          .catch(err => dwAlerts.error(err, 'Failed to query user roles.')),
        lazy: 'lazyLoadSetup',
      }
    });

    $routeProvider.when('/setup/api-settings', {
      template: '<div ng-include="\'api-settings/api-settings.tpl.html\'"></div>',
      controller: 'ApiSettingsCtrl as $ctrl',
      label: 'API Settings',
      routeBranch: 'setup/api-settings',
      translations: ['api-settings'],
      resolve: {
        allowedHosts: function(dwAlerts, allowedHostService, routeHelper) {
          return routeHelper.auth()
            .then(() => allowedHostService.query()
              .catch(err => dwAlerts.error(err, 'Failed to query allowed hosts.')));
        },
        lazy: 'lazyLoadSetup',
      }
    });

    $routeProvider.when('/owners-regions', {
      template: '<div ng-include="\'owners-regions/owners-regions.tpl.html\'"></div>',
      controller: 'OwnersRegionsCtrl as $ctrl',
      label: 'Owners & Regions',
      routeBranch: 'owners-regions',
      translations: ['owners-regions'],
      resolve: {
        owners: (dwAlerts, Owner, routeHelper) => {
          return routeHelper.auth()
            .then(() => Owner.query()
              .catch(err => dwAlerts.error(err, 'Failed to query owners.')));
        },
        regions: (dwAlerts, Region, routeHelper) => {
          return routeHelper.auth()
            .then(() => Region.query()
              .catch(err => dwAlerts.error(err, 'Failed to query regions.')));
        },
        lazy: 'lazyLoadSetup',
      }
    });

    $routeProvider.when('/user-fields', {
      template: '<div ng-include="\'user-fields/user-fields.tpl.html\'"></div>',
      controller: 'UserFieldsCtrl as $ctrl',
      routeBranch: 'user-fields',
      translations: ['form-design'],
      resolve: {
        userFields: (dwAlerts, UserField, routeHelper) => {
          return routeHelper.auth()
            .then(() => UserField.query()
              .catch(err => dwAlerts.error(err, 'Failed to query user fields.')));
        },
        lazy: 'lazyLoadSetup',
      }
    });

    $routeProvider.when('/reservation-fields', {
      template: '<div ng-include="\'reservation-fields/reservation-fields.tpl.html\'"></div>',
      controller: 'ReservationFieldsCtrl as $ctrl',
      routeBranch: 'reservation-fields',
      translations: ['form-design'],
      resolve: {
        auth: (routeHelper) => routeHelper.auth(),
        lazy: 'lazyLoadSetup',
      }
    });

    $routeProvider.when('/integrations-setup', {
      template: '<div ng-include="\'integrations-setup/integrations-setup.tpl.html\'"></div>',
      controller: 'IntegrationsSetupCtrl as $ctrl',
      label: 'Integrations Setup',
      routeBranch: 'integrations-setup',
      translations: ['integrations-setup'],
      resolve: {
        lazy1: 'lazyLoadSetup',
        lazy2: 'lazyLoadAppointmentPlus',
        lazy3: 'lazyLoadQuickBooks',
        lazy4: 'lazyLoadTeamgo',
        lazy5: 'lazyLoadKisi',
        lazy6: 'lazyLoadEzeep',
        lazy7: 'lazyLoadGreetly',
        lazy8: 'lazyLoadDoorflow',
      }
    });

    $routeProvider.when('/bundles', {
      template: '<div ng-include="\'bundles/bundles.tpl.html\'"></div>',
      controller: 'BundlesCtrl as $ctrl',
      routeBranch: 'bundles',
      userTasksPolicy: false,
      // translations: ['bundles'],
      resolve: {
        auth: (routeHelper, session, dwAlerts) => routeHelper.auth().then(() => {
          if (!session.isAdmin()) return dwAlerts.error('Insufficient permissions.');
        }),
        lazy: 'lazyLoadSetup',
      }
    });
  });
