import 'bundles/lazy-loaders.service.js';

export default angular.module('deskworks.terminationRequest', [
  'ngRoute',
  'deskworks.routeHelper',
  'deskworks.lazyLoaders',
])

  .config(function($routeProvider) {
    $routeProvider.when('/termination-request', {
      template: '<div ng-include="\'termination-request/termination-request.tpl.html\'"></div>',
      controller: 'TerminationRequestCtrl as $ctrl',
      routeBranch: 'termination-request',
      translations: ['termination-request'],
      resolve: {
        recurringCharges: (dwAlerts, recurringChargeService, state, routeHelper) =>
          routeHelper.auth()
            .then(() => routeHelper.selectUser())
            .then(() => recurringChargeService.query(state.getCurrentCenterId(), state.getCurrentUserId())
              .catch(err => dwAlerts.error(err, 'Failed to query recurring charges.'))
            ),
        lazy: 'lazyLoadTerminationRequest',
      }
    });
  });
