import 'bundles/lazy-loaders.service.js';
import './import.scss';

export default angular.module('deskworks.import', [
  'deskworks.routeHelper',
  'deskworks.dwAlerts',
  'deskworks.lazyLoaders',
])
  .config(function($routeProvider) {
    $routeProvider.when('/setup/import', {
      template: '<div ng-include="\'wizard/import.tpl.html\'"></div>',
      controller: 'SetupImportCtrl as $ctrl',
      label: 'Setup Import',
      routeBranch: 'setup/import',
      translations: ['reservation-rules', 'user-profile', 'billing-profile', 'products', 'recurring-billing'],
      resolve: {
        auth: routeHelper => routeHelper.auth(),
        lazy: 'lazyLoadImport',
      }
    });
  });
